.ums-fakegraph {
    &__container {
        min-height: 360px;
        padding-top: 36px;
        position: relative;
    }
    &__line {
        border-bottom: 2px solid lighten(@color-border, 8);
        display: block;
        height: 6.2rem;
        &:first-child {
            border-top: 2px solid lighten(@color-border, 8);
        }
    }
    &__number-holder {
        display: flex;
        padding-bottom: 2.5rem;
        & > * {
            flex: 1;
            font-weight: 900;
            opacity: 0.4;
            font-size: 0.8em;
            padding-top: 0.4rem;
            &:last-child {
                flex: 0 1 auto;
            }
        }
    }
    &__content {
        display: flex;
        justify-content: center;
        margin-top: -5rem;
    }
    &__message {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        padding-top: 4rem;
    }   
    &__loader {
        position: absolute;
        width: 100%;
        top: 0;
        bottom: 0; 
        right: 0;
        left: 0;
        opacity: 0;
        animation: showloader .2s .2s ease forwards;
    }
}

@keyframes showloader {
    0% {
        transform: scale(0);
        opacity: 0;
    }
    100% {
        transform: scale(1);
        opacity: 1;
    }
}