// OVERLAY 

.ums-overlay {

	&__fullscreen {
		position: fixed;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		z-index: 999999;
		overflow: hidden;
		& .ums-flexgrid__container {max-width: 100%; margin-left: 0; margin-right: 0;}
	}

	&__scroll-layer {
		position: relative;
		overflow: auto;
		-webkit-overflow-scrolling: touch;
		z-index: 10;
		width: 100%;
		height: 100%;
	}

	&__container {
		position: relative;
		width: 700px; 
		max-width: 90%;
		background: @color-white;
		color: #000;
		border-radius: 10px;
		margin: 80px auto 60px;
		box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.15);
		z-index: 1;
		opacity: 0;
		animation: openContainer 0.5s forwards cubic-bezier(0.01, 0.74, 0.36, 0.96); 

		&--large		{width: 1200px;}

		&--fluid {
			.ums-overlay__header {
				padding-bottom: 5px;
				border-bottom: 0;
			}
			.ums-overlay__content {
				padding-top: 5px;
				padding-bottom: 5px;
				border: 0;
			}
			.ums-overlay__footer {
				border: 0;
				padding-top: 15px;
			}
		}

		&.s-closing 	{animation: closeContainer 0.2s forwards ease-out}
		
	}
	&__closelayer {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background: @color-overlay;
	}


	&__header 		{padding: @m-large @m-large @m-base/2; border-bottom: 1px solid @color-light-b;

		> * 		{margin: 0;

			& + * 	{margin-top: @m-small/2;}

		} 

	}

	&__subnav 			{border-top: 0;} 

	&__content 			{padding: @m-base @m-large;

		&__grid 		{margin: 0; padding: 0; width: 100%;} 
		
		&__item 		{margin: 0; padding-left: @m-large !important; padding-right: @m-large !important;

			&--gray 	{background: @color-light-a;}

			&__title 	{display: inline-block; margin-bottom: @m-small/2;}}

		&--dark 		{background: fade(@color-light-a, 30%);}

		&--nopadding 	{padding:0;}

	}

	&__code--unable 	{color: @color-gray; padding: 10px 20px;}


	&__footer 			{padding: @m-base @m-large; border-top: 1px solid @color-light-b; display: flex;
		&:empty 		{display: none;}
		&--justify-end 		> *	{justify-content: flex-end; display: flex; width: 100%;}

		&--justify-between 	> *	{justify-content: space-between; display: flex; width: 100%;}

		&__items 		{
			& > * + * {margin-left: 10px;}

		}
	}

	&__close 			{background: rgba(0,0,0,0.0);  text-decoration: underline; margin: 5px; font-weight: @fw-bold; border-radius: @br-base/2; color: @color-gray; padding: @m-small/1.5 @m-small; display: inline-block; position: absolute; top: 0; left: auto; right: 0; line-height: 1; display: flex; flex-flow: row wrap; align-items: center; cursor: pointer; .transition;
		
		&:hover 		{background: rgba(0,0,0,0.1); color: @color-base;}

		* + * 			{margin-left: 10px; font-weight: @fw-bold;}

	}

	&__magic-list {
		margin-left: 30px;
		margin-top: 40px;
		& > li {
			position: relative;
			& > strong {
				display: block;
				margin-top: -20px;
				position: absolute;
				left: -30px;
			}
			& > a {
				display: list-item;
				&.s-first {
					margin-top: 30px;
				}
			}
		}
	}

	@keyframes openContainer {
		0% {opacity: 0; transform: translateY(100px);}
		100% {opacity: 1; transform: translateY(0px);}
	}
	@keyframes closeContainer {
		0% {opacity: 1; transform: translateY(0px);}
		100% {opacity: 0; transform: translateY(100px);}
	}

}