

// Page Menu

.ums-pagemenu {

	&__title  		{display: flex; flex-flow: row wrap; width: 100%; align-items: center; margin-bottom: @m-small;

		ums-icon 	{margin-right: @m-base/3;}}

	&__list 		{width: 100%; list-style: none; margin: 0; padding: 0; border-top: 1px solid @color-light-b;

		strong 		{color: @color-main; margin-bottom: 5px;}}

	&__item 		{padding: @m-base/2 @m-large @m-base/2 @m-small; border-bottom: 1px solid @color-light-b; position: relative; .transition;

		ums-icon 	{position: absolute; left: auto; right: @m-small/2; top: 0; bottom: 0; margin: auto 0; .transition;}

		&.s-active  {background: fade(@color-light-a, 50%)}}

	&__item:hover 	{background: fade(@color-light-a, 25%); cursor: pointer; 

		/* .ums-pagemenu__description 	{white-space: normal;} */

		ums-icon 	{right: @m-small/3;}}

	&__description 	{color: @color-base; font-weight: @fw-bold; .fz-sm(); max-width: 100%; white-space: nowrap; overflow: hidden; text-overflow: ellipsis; max-width: 100%;

		* 			{margin-right: 5px;} 
 
	}
 
}     