

// FILTER

.ums-filter {
		
	&__container 	{display: flex; flex-flow: row wrap; justify-content: space-between; margin: -@m-small/2 -@m-small/2 0;

		&--right 	{text-align: right; justify-content: flex-end; margin-bottom: -@m-base;}}
	
	&__row 			{display: flex; flex-flow: row wrap; align-items: flex-start; align-content: flex-start; padding: 0 @m-small/2; width: 100%; margin-top: @m-small/2; margin-bottom: -@m-small/3 !important;
		
		& > .ums-filter__item, & > .ums-form__checkbox__label, & > .ums-filter__label {
			margin: 0 @m-small/3 @m-small/3 0;
			flex: 1 0 auto;
		}
	
	}

	&__row:nth-of-type(1) 	{width: 100%; margin-bottom: @m-small;}

	&__row:nth-of-type(2)  	{max-width: 40%;}

	&__row:nth-of-type(3)  	{max-width: 60%;}


	@media @min1300 	{

		&__row:nth-of-type(1) 	{width: 100%; max-width: 40%; margin-bottom: 0;}

		&__row:nth-of-type(2) 	{flex: 1 1 0; max-width: 40%;}

		&__row:nth-of-type(3) 	{flex: 1 1 0; max-width: 100%;}
	
	}

	&__row--auto, 
	&__row--auto:nth-of-type(1),
	&__row--auto:nth-of-type(2),
	&__row--auto:nth-of-type(3) {max-width: none; align-items: flex-end; width: auto; flex: 0 0 auto;}

	&__item {display: flex; flex-direction: column; 
		& + &__item {
			margin-left: 20px;
		}
	}

}