

// ANIMATION  
@fadeDown: {
	opacity: 0;
 	animation-name: fadeDown;
  	animation-timing-function: ease;
  	animation-duration: .4s;
	animation-fill-mode: forwards; 

	.anim-stagger (@i) when (@i > 0) {
		&:nth-of-type(@{i}) {
			animation-delay: (@i / 12.5s);
		}
		.anim-stagger(@i - 1);
	}
	.anim-stagger (20);
}

@fadeAnim: {
	opacity: 0;
 	animation-name: fade;
  	animation-timing-function: ease-out;
  	animation-duration: .25s;
	animation-fill-mode: forwards; 

	.anim-stagger (@i) when (@i > 0) {
		&:nth-of-type(@{i}) {
			animation-delay: (@i / 22s);
		}
		.anim-stagger(@i - 1);
	}
	.anim-stagger (20);
}

@fadeUp: {@fadeDown(); animation-name: fadeUp !important;}

.is-animated 		{@fadeUp();}
.is-fade-animated 	{@fadeAnim();}

@keyframes 	fadeDown {
  	from 	{opacity: 0; transform: translateY(-@m-small);}
  	to 		{opacity: 1; transform: translateY(0);} 
} 

@keyframes 	fadeUp {
  	from 	{opacity: 0; transform: translateY(@m-small);}
  	to 		{opacity: 1; transform: translateY(0);} 
} 

@keyframes 	fade {
  	from 	{opacity: 0;}
  	to 		{opacity: 1;} 
} 


// BD 26-04-2021 Should be replaced by a JS staggered animation in the future to prevent huge stylesheets
.ums-table__tr--inside-box-nested.is-fade-progress {
	
   .anim-stagger (@i) when (@i > 0) {

		&:nth-of-type(@{i}) {
			& > td {    
				&:not(:nth-of-type(1)) {
					&::after {
						opacity: 0;
						animation: fade .2s ease forwards;
						animation-delay: (@i / 20s);
					}
		
					& > * {
						opacity: 0;
						animation: fade .2s ease forwards;
						animation-delay: (@i / 20s);
						
						&.ums-profile__type-holder {
							animation-delay: (@i / 12s);
						}
					}
				}
		
				& .ums-profile__line-item--inside {
					opacity: 0;
					animation: fade .15s ease forwards;
					animation-delay: (@i / 20s);
				}
			}			
		}
		.anim-stagger(@i - 1);
	}
	.anim-stagger (20);
}