// LINKBOX

.ums-linkbox {
	display: block;
	padding: @m-base / 1.5 @m-base;
	margin-bottom: @m-base;
	position: relative;
	border: 1px solid @color-border;
	box-shadow: @bs-base;
	border-radius: @br-base;
	text-decoration: none !important;
	overflow: hidden;
	background: @color-white;
	color: @color-main;
	.transition;
	&[data-has-link]:hover, &[data-has-link]:focus {
		background: darken(@color-white, 3);
	}

	&--grid {
		display: grid;
		grid-template-areas: "icon title" "icon link";
		grid-template-columns: auto 1fr;
		grid-template-rows: auto auto;
		column-gap: 15px;
		row-gap: 5px;

		@media @min1300 {
			column-gap: 30px;
		}

		& > [class*="__title"] {
			grid-area: title;
			align-self: end;
			margin: 0;
		}
		& > [class*="__bigicon"] {
			grid-area: icon;
			align-self: center;
		} 
		& > [class*="__button"] {
			grid-area: link;
			align-self: start;
			color: @color-main;
		}
	}

	&--license {
		background: @color-main;
		color: @color-white;
		&[data-has-link]:hover, &[data-has-link]:focus {
			background: lighten(@color-main, 3);
		}
		[class*="__button"] {
			color: inherit !important;
		}

		&[data-license-state="active"]  {
			[class*="__state__indicator"] {
				background: @color-success;
			}
		}
		&[data-license-state="warning"]  {
			[class*="__state__indicator"] {
				background: @color-warning;
			}
		}
		&[data-license-state="expired"]  {
			[class*="__state__indicator"] {
				background: @color-error;
			}
		}
		&[data-license-state="corrupt"] {
			background: @color-base;
			&[data-has-link]:hover, &[data-has-link]:focus {
				background: lighten(@color-base, 3);
			}
			[class*="__state__indicator"] {
				background: @color-error;
			}
		}
	}
	&--license-error-message {
		background: @color-alt;
		color: @color-black;

		margin-top: -34px;
		border-top-left-radius: 0;
		border-top-right-radius: 0;
		border-top: 0;
		padding-top: 13px;
		padding-bottom: 13px;
		&[data-has-link]:hover, &[data-has-link]:focus {
			background: darken(@color-alt, 3);
		}

		@media @min1300 {
			padding-top: 23px;
			padding-bottom: 23px;
		}
		& > p {
			margin: 0;
		}
		&[aria-hidden="true"] {
			display: none;
		}
	}

	&__title {
		color: inherit;

		&--big {
			font-size: 20px !important;
			@media @min1300 {
				font-size: 30px !important;
			}
		}
		&--margin-bottom-0 {
			margin-bottom: 0 !important;
		}
	}

	&__state {
		display: flex;
		align-items: center;
		font-weight: @fw-heavy;

		&__indicator {
			width: 12px;
			height: 12px;
			background: @color-white;
			border-radius: 50%;
			margin-right: 10px;
		}
	}

	&__table {
		margin-top: 20px;
		border: 0;
		border-collapse: collapse;
		background: transparent;
		text-align: left;

		& tr > * + * {
			padding-left: 20px;
		}
		& th {
			font-weight: 500;
		}
		& td {
			font-weight: 700;
		}
	}

	&__tbody + .ums-linkbox__tbody:before {content:"@"; display: block; line-height: 10px; text-indent:-99999px;}


	&__subtitle {
		color: inherit;
		opacity: 0.5;
	}

	&__button {
		&--base-color {
			color: @color-base !important;
			&:hover, &:focus {
				color: lighten(@color-base, 20) !important;
			}
		}
		&--white-color {
			color: @color-white;
			&:hover, &:focus {
				color: darken(@color-white, 15) !important;
			}
		}
		&--margin-top {
			margin-top: 20px;
		}
		/*&--link-on-parent::before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			display: inline;
			z-index: 1;
		}*/
	}

	&__domainlist {padding: 0; list-style: square; margin: 0px 0 0 15px;
		li {color: @color-white;}
	}

	&__bigicon {
		width: 50px;
		height: 50px;
		border-radius: 50%;
		background: @color-base;
		color: @color-white;
		display: flex;
		align-items: center;
		justify-content: center;
		& > * {
			width: 20px;
			height: 20px;
		}
		@media @min1300 {
			width: 70px;
			height: 70px;
			& > * {
				width: 30px;
				height: 30px;
			}
		}
	}
	&__icon {
		color: #68676B;
	}
	&__fingerprint {
		width: 150px;
		height: 200px;
		position: absolute;
		top: 0;
		right: 0;
		left: auto;
		bottom: auto;
		& > img, & > svg {
			fill: #fff;
			transform: rotate(45deg) translate(0px, -70px);
    		opacity: 0.05;
		}
	}

	@media @min1600 {
		padding: @m-base @m-base*1.5;
	}
	&__packagestatus-inner-message {
		margin-top: 1rem;
	}
}
