.iframe-holder {
  border: 1px solid @color-light-c;
  border-radius: @br-base;
  padding: 20px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
}

.iframe-box {
  --mouse-y: 0px;
  width: var(--width);
  max-width: 100%;
  height: var(--height);
  position: relative;
  overflow: hidden;
  margin: 0 auto;
  border-radius: @br-base;
  grid-row: 1;
  grid-column: 1;
}

iframe {
  width: 100%;
  height: var(--height);
  pointer-events: none;
  position: absolute;
  top: 0;
  background: none;
}

.indicator {
  position: absolute;
  left: 0;
  right: 0;
  top: var(--mouse-y);
  z-index: 4;
  pointer-events: none;
  display: flex;
  transform: translateY(-14px);
}

.indicator::after {
  content: "";
  border-bottom: 1px solid black;
  width: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  transform: translateY(-50%);
}

.indicator-box {
  position: relative;
  z-index: 1;
  background: #000;
  color: #fff;
  padding: 5px 10px;
  margin: 0 auto;
  font-family: "Lato", sans-serif;
  font-size: 14px;
  border-radius: 5px;
}

.scrollmap {
  width: 100%;
  height: var(--height);
  background: linear-gradient(-180deg, red var(--n80), yellow var(--n60), green var(--n40), lightblue var(--n20), darkblue, black);
  z-index: 1;
  opacity: 0.2;
  position: absolute;
  top: 0;
  left: 0;
}

.scrollmap.blend {
  opacity: 0.5;
  mix-blend-mode: darken;
}

.ums-hmap {
  &__lines {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
  }
  &__line {
    --background: #000;
    --top-pos: 0;
    position: absolute;
    left: 0;
    right: 0;
    top: var(--top-pos);
    z-index: 3;
    pointer-events: none;
    display: flex;

    &--avg-fold {
      --background: @color-main;
    }
    
    &::after {
      content: "";
      border-bottom: 1px solid var(--background);
      width: 100%;
      position: absolute;
      top: 0;
      bottom: 0;
    }
  }
  &__line-header {
    padding: 6px 20px;
    background: var(--background);
    color: #fff;
    border-top-right-radius: @br-base;
    font-weight: bold;
    position: absolute;
    bottom: 0;
  }
  &__legend {
    pointer-events: none;
    width: 50px;
    position: sticky;
    bottom: 20px;
    right: 0;
    background: @color-main;
    color: #fff;
    border-radius: @br-base;
    gap: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 4;
    padding: 10px;
    grid-column: 1;
    grid-row: 1;
    justify-self: end;
    align-self: end;
  }
  &__legend-gradient {
    background: linear-gradient(-180deg, red 10%, yellow, green, lightblue, darkblue, black);
    height: 150px;
    width: 10px;
    border-radius: @br-base;
  }
}
