

// TEST INFORMATION

.ums-testinformation	{

	&__container 		{}

	&__row 				{.fz-md(); padding: @m-small/3 0; display: flex; flex-flow: row wrap;}

	&__row + .ums-testinformation__row  	{border-top: 1px solid fade(@color-border, 50%);}

	&__row strong 		{display: inline-block; padding-right: @m-small/3; width: 120px;}
	
	&__row  ul 			{margin-left: 136px; margin-top: 3px; margin-bottom: 0;}

	&__row  ul li 		{opacity: .5;}

}