// LOADER

.ums-loader {
	
	&__fixed 	{pointer-events: none; position: fixed; top: 55px; bottom: 0; left: 0; right: 0;  margin: auto; z-index: 999; display: flex; flex-flow: row wrap; align-items: center; justify-content: center; background: fade(@color-gray, 75%);}

	
	/* small version */

	&__small 			{

		&__container 	{width: 300px; height: 300px; display: flex; flex-flow: row wrap; justify-content: center; align-items: center;}

	}


	/* large version */

	&__large 			{

		&__container 	{width: 600px; height: 400px;}

		&__container 	#loaderAnimation {width: 100%; height: 100%;}

		&__text 		{.fz-xxl(); font-weight: @fw-heavy; color: @color-white; width: 100%; text-align: center; margin-top: -20px;}

	}
	
} 


// LOADER ANIMATED

.ums-loader__animation {

	.loader-inner {
	    bottom: 0;
	    height: 60px;
	    left: 0;
	    margin: auto;
	    position: absolute;
	    right: 0;
	    top: 0;
	    width: 100px;
	}

	.loader-line-wrap {
	    animation: spin 2000ms cubic-bezier(.175, .885, .32, 1.275) infinite;
	    box-sizing: border-box;
	    height: 50px;
	    left: 0;
	    overflow: hidden;
	    position: absolute;
	    top: 0;
	    transform-origin: 50% 100%;
	    width: 100px;
	}
	.loader-line {
	    border: 4px solid transparent;
	    border-radius: 100%;
	    box-sizing: border-box;
	    height: 100px;
	    left: 0;
	    margin: 0 auto;
	    position: absolute;
	    right: 0;
	    top: 0;
	    width: 100px;
	}
	
	.loader-line-wrap:nth-child(1) { animation-delay: -50ms; }
	.loader-line-wrap:nth-child(2) { animation-delay: -100ms; }
	.loader-line-wrap:nth-child(3) { animation-delay: -150ms; }
	.loader-line-wrap:nth-child(4) { animation-delay: -200ms; }
	.loader-line-wrap:nth-child(5) { animation-delay: -250ms; }

	.loader-line-wrap:nth-child(1) .loader-line {
	    border-color: @color-white;
	    height: 90px;
	    width: 90px;
	    top: 7px;
	}
	.loader-line-wrap:nth-child(2) .loader-line {
	    border-color: @color-main;
	    height: 76px;
	    width: 76px;
	    top: 14px;
	}
	.loader-line-wrap:nth-child(3) .loader-line {
	    border-color: @color-success;
	    height: 62px;
	    width: 62px;
	    top: 21px;
	}
	.loader-line-wrap:nth-child(4) .loader-line {
	    border-color: @color-error;
	    height: 48px;
	    width: 48px;
	    top: 28px;
	}
	.loader-line-wrap:nth-child(5) .loader-line {
	    border-color: @color-alt;
	    height: 34px;
	    width: 34px;
	    top: 35px;
	}

	.loader-line-wrap.gray:nth-child(1) .loader-line {
		border-color: darken(@color-white, 10);
	}
	.loader-line-wrap.gray:nth-child(2) .loader-line {
		border-color: darken(@color-white, 13);
	}
	.loader-line-wrap.gray:nth-child(3) .loader-line {
		border-color: darken(@color-white, 16);
	}
	.loader-line-wrap.gray:nth-child(4) .loader-line {
	    border-color: darken(@color-white, 11);
	}
	.loader-line-wrap.gray:nth-child(5) .loader-line {
	    border-color: darken(@color-white, 8);
	}

	@keyframes spin {
	    0%, 15% {
			transform: rotate(0);
		}
		100% {
			transform: rotate(360deg);
		}
	}
}