

// COLORS 

@color-white: 		#FFFFFF;   /* white */
@color-black: 		#000000;   /* black */
@color-tint: 		#FCFCFC;   /* light */

@color-base: 		#1A254F;   /* darkblue 	(font) */
@color-gray: 		#68676B;   /* darkgray 	(font) */

@color-main: 		#3444B2;   /* blue 			(menu) */
@color-alt: 		#F5C1BC;   /* umbraco pink	(active menu item) */
@color-beige: 		#F2EBE7;   
 

@color-light-a: 	#EEEEF0;   /* border */
@color-light-b: 	#E3E5E9;   /* border */
@color-light-c: 	#D1D4DC;   /* border */

@color-graph01: 	#2C7DFF;   /* graph 01 */
@color-graph02: 	#FFBA00;   /* graph 02 */
@color-graph03: 	#FF7F50;   /* graph 03 */
@color-graph04: 	#000000;   /* graph 04 */
@color-graph05: 	#000000;   /* graph 05 */
@color-graph06:		#2AC37C;
@color-graph07:		#F43D1F;
@color-graph08:		#1F2953;
@color-graph00: 	#4C69E5;   /* used for others */ 

@color-success: 	#11BC9B;
@color-conversion: 	#FFBA00;
@color-error: 		#FC391C;
@color-warning:		#FF7F50;

@color-border:  	@color-light-c;
@color-overlay: 	fade(@color-black, 35%);
 

// MARGINS

@m-small: 		20px;
@m-base: 		30px;
@m-large: 		60px;


// Z-INDEX

@z-content: 	10;
@z-header: 		50;
@z-overlay: 	100;


// SIZES

@max-width: 	1500px;
@nav-height: 	55px;
@topbar-height: 100px;


// BORDER RADIUS

@br-base: 		5px;
 

// BOX SHADOW

@bs-base: 		0 2px 2px fade(@color-black, 5%);
@bs-hover: 		0 4px 4px fade(@color-black, 10%);


// RESPONSIVE

@min1300: 		~"screen and (min-width: 1366px)";
@min1600: 		~"screen and (min-width: 1599px)";

@max1650:       ~"screen and (max-width: 1649px)";

// FONTS

.fz-xs() 		{font-size: 7px !important;    @media @min1300 {font-size: 9px !important;}}
.fz-sm() 		{font-size: 9px !important;    @media @min1300 {font-size: 11px !important;}}
.fz-md() 		{font-size: 11px !important;   @media @min1300 {font-size: 13px !important;}}
.fz-lg() 		{font-size: 13px !important;   @media @min1300 {font-size: 15px !important;}}
.fz-xl() 		{font-size: 18px !important;   @media @min1300 {font-size: 20px !important;}}
.fz-xxl() 		{font-size: 24px !important;   @media @min1300 {font-size: 26px !important;}}

@fw-heavy: 		900; 
@fw-bold: 		700;


// OTHER

@root: /App_Plugins/uMarketingSuite;
@assets: @{root}/assets;
@vh: {position: absolute; height: 1px; width: 1px; overflow: hidden; clip: rect(1px,1px,1px,1px); margin: 0;}
.transition(@duration: 0.2s, @ease: ease-in-out){transition: all @duration @ease;}
