

// SAVEBAR

.ums-savebar {
		
	&__container 	{position: fixed; top: auto; bottom: 0; z-index: 50; width: 100%; background: @color-white; border-top: 1px solid @color-border; height: 50px; padding: 10px 20px; display: flex; flex-flow: row wrap; align-items: center; justify-content: flex-end; left: 0;}

	&__container .ums-button 	{padding: 6px 14px !important; font-size: 15px !important; line-height: 20px !important; border: 0 !important; height: 32px !important; margin-left: 5px !important;}

} 