// TABLE

.ums-table {
  &__table {
    width: 100%;
    background: none;
    border-collapse: inherit;
    background: @color-white;
    border: 1px solid @color-border;
    box-shadow: @bs-base;
    color: @color-black;
    border-radius: @br-base @br-base 0 0;
    .transition;

    &--boxed {
      background: transparent;
      border: 0;
      box-shadow: 0;

      tbody:before {
        content: "@";
        display: block;
        line-height: 10px;
        text-indent: -99999px;
      }

      tbody tr,
      thead {
        box-shadow: @bs-base;
      }

      tbody tr td,
      thead tr td,
      thead tr th {
        background: @color-white;
        border-top: 1px solid @color-border;
        border-bottom: 1px solid @color-border;
        line-height: 1.2;
      }

      tbody tr td:nth-of-type(1),
      thead tr td:nth-of-type(1),
      thead tr th:nth-of-type(1) {
        border-left: 1px solid @color-border;
      }

      tbody tr td:nth-last-of-type(1),
      thead tr td:nth-last-of-type(1),
      thead tr th:nth-last-of-type(1) {
        border-right: 1px solid @color-border;
      }
    }

    &--analytics {
      & thead > tr > [class*="--analytics-border"],
      & tbody > tr > [class*="--analytics-border"] {
        border-right: 1px solid @color-border;
        & + * {
          padding-left: 40px;
        }
      }
    }

    &--no-minwidth {
      & [class*="__td"],
      & [class*="__th"] {
        min-width: 0;
      }
    }

    &--fullheight {
      height: 100%;
    }

    &--overflowing-body {
      thead,
      tbody {
        display: block;
      }

      tbody {
        height: 100px; /* Just for the demo          */
        overflow-y: auto; /* Trigger vertical scroll    */
        overflow-x: hidden; /* Hide the horizontal scroll */
      }
      th,
      td,
      tr {
        display: block;
        width: 100%;
      }
    }
    &--profile-activity {
      & .ums-table__th,
      & .ums-table__td {
        padding-left: 8px;
        padding-right: 8px;
      }
    }
    &--faketable {
      &,
      thead,
      tbody,
      tfoot {
        display: flex;
        flex-direction: column;

        & > tr {
          display: flex;
          & > th,
          & > td {
            flex: 1;

            &:not([class*="--fixed-"]) {
              max-width: 100% !important;
            }
          }
        }
      }
      & tbody {
        overflow-y: auto;
        flex: 1;
        border-top: 1px solid @color-border;

        & > tr {
          &:first-of-type {
            & > th,
            & > td {
              border-top: 0;
            }
          }
          &:last-of-type {
            border-bottom: 1px solid @color-border;
          }
        }
        &.ums-table__tbody--transparent tr {
          border-bottom: 0;
        }

        /* width */
        &::-webkit-scrollbar {
          width: 6px;
        }

        /* Track */
        &::-webkit-scrollbar-track {
          background: #f6f6f7;
          border-left: 1px solid @color-border;
        }

        /* Handle */
        &::-webkit-scrollbar-thumb {
          background: #c1c1c1;
        }

        /* Handle on hover */
        &::-webkit-scrollbar-thumb:hover {
          background: #555;
        }
      }
      & tfoot {
        margin-top: -1px;
      }
    }

    &--nested {
      border: 0;
      &__parent {
        margin: 0;
        padding: 0 !important;
        border-top: 0 !important;
      }
      & tbody::before {
        display: none !important;
      }
      & thead,
      tbody,
      tr,
      th,
      td {
        border: 0 !important;
        border-radius: 0 !important;
        box-shadow: none !important;
      }
    }

    &.s-closed {
      border-radius: @br-base;
      background: #f6f4f5;

      * {
        color: @color-gray !important;
      }

      #icon-toggle {
        transform: rotate(180deg);
        .transition;
      }

      .ums-table__tbody,
      .ums-table__tr--labels {
        display: none;
      }
    }

    &.s-closed:hover {
      box-shadow: @bs-hover;
    }

    &.s-toggle .ums-table__thead:hover,
    &.s-closed .ums-table__thead:hover {
      cursor: pointer;

      .ums-button {
        background: @color-light-b;
      }
    }
  }

  &__table + .ums-table__table {
    margin-top: @m-base;
  }
  // thead
  &__thead {
    td {
      border-top: 0;
      border-bottom: 1px solid @color-border;
    }
    &__content {
      & > p {
        line-height: 1.6;
        margin-top: @m-small;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }

    th {
      border-top: 1px solid @color-border;
    }

    tr:nth-of-type(1) th {
      border-top: 0px;
    }

    &--labels-alt {
      box-shadow: none !important;
    }
  }
  // tbody
  &__tbody {
    tr:nth-of-type(even) td {
      background: fade(@color-light-a, 35%);
    }

    td:nth-of-type(1):nth-last-of-type(1) {
      max-width: 75%;
    }

    &--ranked {
      > tr td:nth-of-type(1) {
        position: relative;

        &:before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          width: 5px;
          height: 100%;
          display: block;
          border-right: 1px solid @color-border;
          background: fade(@color-border, 50%);
        }
      }

      > tr.is-rank01 td:nth-of-type(1):before {
        background: @color-graph01;
      }

      > tr.is-rank02 td:nth-of-type(1):before {
        background: @color-graph02;
      }

      > tr.is-rank03 td:nth-of-type(1):before {
        background: @color-graph03;
      }
    }
    &--transparent {
      tr:not(:first-of-type) {
        th,
        td {
          background: transparent !important;
          border: 0;
        }
      }
    }

    &--transparent-all {
      th,
      td,
      tr {
        background: transparent !important;
        border: 0;
      }
    }

    &--tight {
      td {
        padding-top: 10px;
      }
      & tr:last-of-type > td {
        padding-bottom: 20px;
      }
    }
    &--soon {
      position: relative;
      &::before {
        content: "Coming soon";
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        text-transform: uppercase;
        font-weight: 600;
        padding-bottom: 40px;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        color: #d4d4d4;
        background: #f7f7f7;
      }
    }
  }
  // tr
  &__tr {
    &--labels {
      box-shadow: none !important;

      th {
        padding-top: @m-small / 3;
        padding-bottom: @m-small / 3;
        background: fade(@color-light-a, 50%) !important;
        border-top: 0 !important;
      }
    }

    &--labels-alt {
      box-shadow: none !important;

      th {
        padding-top: @m-small / 4;
        padding-bottom: 0;
        background: none !important;
        border: 0 !important;
      }
    }

    &--sm td {
      padding: @m-small / 2 @m-small / 2 !important;
      .fz-md();
    }

    &--dark td {
      padding: @m-small / 3 @m-small / 3;
      font-style: italic;
    }

    &--wide td {
      padding-top: @m-base;
      padding-bottom: @m-base;
    }

    &--tight td {
      padding-top: 0;
      padding-bottom: @m-base / 2 + 4px;
    }

    &--lg td {
      padding: @m-base @m-small / 2;
      min-width: 110px;
    }

    &--inside-box {
      & > th {
        background: #fff;
        padding-bottom: 0;
        &:first-of-type {
          border-left: 1px solid #d1d4dc;
        }
      }
      & > td,
      &-nested > th {
        border-top: 0 !important;
        border-bottom: 0 !important;
        background: #fff !important;

        &::before {
          content: "";
          background: fade(@color-light-a, 55%) !important;
          position: absolute;
          top: 5px;
          bottom: 5px;
          left: 0;
          right: 0;
        }
        & > span {
          position: relative;
          z-index: 2;
        }

        &:first-of-type::before {
          border-radius: 5px 0 0 5px;
          left: 30px;
        }
        &:last-of-type::before {
          border-radius: 0 5px 5px 0;
          right: 30px;
        }
      }

      &.s-open {
        & > td::before {
          border-bottom-left-radius: 0;
          border-bottom-right-radius: 0;
        }
      }

      &-toggle {
        cursor: pointer;

        & > td,
        & ~ tr .ums-table__th,
        & ~ tr .ums-table__td {
          transition: background 0.15s ease;
        }

        &:hover {
          & > td,
          & ~ tr .ums-table__th,
          & ~ tr .ums-table__td {
            &::before {
              background: fade(@color-light-a, 75%) !important;
            }
          }
        }
      }

      &-nested {
        & > td,
        & > th {
          &::before {
            content: "";
            background: fade(@color-light-a, 55%) !important;
            position: absolute;
            top: 0px;
            bottom: 0px;
            left: 0;
            right: 0;
          }
        }

        & > th {
          padding-top: 0;

          &::before {
            border-radius: 0 !important;
            top: -5px;
          }
        }

        & > td {
          padding-top: 20px;
          padding-bottom: 20px;

          &:not(:nth-of-type(1)) {
            &::after {
              content: "";
              background: #fff;
              position: absolute;
              top: 3px;
              bottom: 3px;
              left: 0;
              right: 0;
              z-index: 0;
            }

            &:first-of-type::after {
              border-radius: 5px 0 0 5px;
              left: 30px;
            }

            &:last-of-type::after {
              border-radius: 0 5px 5px 0;
              right: 50px;
            }
          }

          &:first-of-type::before,
          &:last-of-type::before {
            border-radius: 0;
          }

          &:nth-of-type(2)::after {
            border-radius: 5px 0 0 5px;
          }
        }

        &:last-of-type > td {
          padding-bottom: 40px;

          &:not(:nth-of-type(1)) {
            &::after {
              bottom: 23px;
            }
          }

          .ums-icon {
            top: -10px;
            position: relative;
          }

          &:first-of-type::before {
            border-bottom-left-radius: 5px;
          }

          &:last-of-type::before {
            border-bottom-right-radius: 5px;
          }

          & > .ums-profile__line-item--inside {
            height: ~"calc(100% - 20px)";
            top: 0;

            &::after {
              opacity: 0 !important;
            }
          }
        }

        & > span {
          position: relative;
          z-index: 1;
        }

        &.s-conversion {
          & > td::after {
            background: #fff8e5;
          }
        }
        &-loading {
          & > td:not(:nth-of-type(1))::after {
            background: rgba(0, 0, 0, 0.02);
          }
        }
      }
    }

    &--specs {
      & > td {
        border-top: 0 !important;
        border-bottom: 0 !important;
        background: #fff !important;
      }
    }

    &--toggle:hover {
      cursor: pointer;
    }

    &--toggle:hover td {
      cursor: pointer;
      background: lighten(@color-light-a, 3%) !important;
      .transition;
    }

    &--toggle:hover .ums-table__toggle {
      opacity: 1;
    }

    &.s-open {
      td {
        border-bottom-color: #fff;
        border-bottom-width: 0;
      }

      & ~ tr {
        display: table-row !important;
        position: relative;
      }

      & ~ tr td {
        background: lighten(@color-light-a, 5%);
      }

      & ~ tr + tr td {
        border-top: 0;
      }

      .ums-table__toggle .ums-icon {
        transform: scaleY(-1);
      }

      & + [class*="--inside-box"] {
        & th,
        &,
        td {
          border-top: 0;
        }
      }
    }

    &.s-inactive {
      color: lighten(#000, 70%);
    }
    /* state: warning */
    &--warning td {
      background: lighten(@color-warning, 32%) !important;
      border-color: fade(@color-warning, 50%) !important;
      border-top: 1px solid fade(@color-warning, 50%) !important;
    }

    &--warning:hover td {
      background: lighten(@color-warning, 27%) !important;
    }

    &--warning td:nth-of-type(1) {
      border-color: fade(@color-warning, 50%) !important;
    }
    /* state: winner */
    &--winner td {
      position: relative;
      background: lighten(@color-conversion, 45%) !important;
      border-color: fade(@color-conversion, 50%) !important;
      border-top: 1px solid fade(@color-conversion, 50%) !important;
    }

    &--winner:hover td {
      background: lighten(@color-conversion, 40%) !important;
    }

    &--winner td:nth-of-type(1) {
      border-color: fade(@color-conversion, 50%) !important;
    }
    /* state: leading */
    &--leading {
      position: relative;
    }

    &--leading .leading-sticker {
      font-weight: @fw-bold;
      .fz-sm();
      background: @color-conversion;
      color: @color-white;
      z-index: 1;
      font-weight: 700;
      position: absolute;
      top: 4px;
      left: -5px;
      width: auto;
      display: block !important;
      padding: 3px 8px 3px 15px;
      border-radius: 1px 5px 5px 1px;
    }

    &--leading .leading-sticker--lower {
      top: 14px;
    }

    &:first-of-type > .ums-table__td--half {
      padding-top: @m-small;
    }

    &:last-of-type > .ums-table__td--half {
      padding-bottom: @m-small;
    }

    /* state: disabled */
    &--disabled td,
    &.s-inactive td > * {
      opacity: 0.5;
    }
  }

  // td / th
  &__th,
  &__td {
    padding: @m-small @m-small;
    .fz-md();
    text-align: left;
    border-top: 1px solid @color-border;
    vertical-align: middle;
    position: relative;

    > * {
      line-height: 1.2;
    }

    &--sm {
      padding: @m-small / 3 @m-small !important;
    }

    &--holds-button {
      padding-top: 10px;
      padding-bottom: 10px;
    }

    &--nowrap {
      white-space: nowrap;
    }

    &--align-top {
      vertical-align: top;
    }

    &--nopadding {
      padding-top: 0;
      padding-bottom: 0;
    }

    &--extra-padding {
      padding-top: 1.7rem;
      padding-bottom: 1.7rem;
    }
    &--check-before {
      background: fade(@color-light-a, 50%) !important;
      padding-left: 10px !important;
      padding-right: 10px !important;
      border-right: 1px solid @color-border;
    }

    &--check-padding {
      padding-left: 20px !important;
      padding-right: 0px !important;
    }

    &--column {
      display: flex;
      flex-direction: column;
      padding: 0;

      &-padding {
        padding: 10px 0;
      }

      & > * + * {
        margin-top: 5px;
      }
    }

    &--hide-overflow {
      overflow: hidden;
      max-width: 200px;
      text-overflow: ellipsis;

      @media @min1300 {
        max-width: 400px;
      }
    }

    &--analytics {
      padding-top: 18px;
      padding-bottom: 18px;

      & [class*="__item-contents"] {
        display: flex;
        width: 100%;
        justify-content: space-between;
        align-items: stretch;
      }
      & [class*="__item-contents--center"] {
        align-items: center;
      }
    }

    &:first-child {
      padding-left: @m-base;
    }

    &:last-child {
      padding-right: @m-base;
    }
  }

  &__td {
    min-width: 160px;

    &--toggle {
      width: 60px;
    }

    &--right {
      text-align: right;
    }
    &--analytics {
      justify-content: space-between;
    }
    &--empty-state {
      width: 100%;
      height: 3.8rem;
    }
    &--empty-border {
      border: 0;
    }
    &--half {
      min-width: 0;
      width: 50%;
      padding-top: @m-small / 2;
      padding-bottom: @m-small / 2;
    }
  }

  &__th {
    &.s-sorting > .umsLabel {
      opacity: 1;
    }
    &--fixed {
      @media @min1300 {
        &-xl {
          width: 480px;
        }

        &-lg {
          width: 300px;
        }

        &-sm {
          width: 180px;
        }

        &-xs {
          width: 100px;
          max-width: 100px;
        }

        &-xxs {
          width: 60px;
          max-width: 60px;
        }

        &-xxxs {
          width: 30px;
          padding: 0;
        }
      }
    }
  }
  &__inner-flex {
    display: flex;
    align-items: center;
  }
  &__inner-column {
    display: flex;
    flex-direction: column;
  }
  // WINNER SELECTION
  &__winnerselect {
    &__container--nowinner:after {
      content: "@";
      display: block;
      line-height: 30px;
      text-indent: -99999px;
    }

    &__status {
      text-align: right !important;
    }

    &__status * {
      opacity: 0;
    }

    &__input {
      visibility: hidden;
      margin: 0;
      width: 0;
      height: 0;
      overflow: hidden;
      display: none;
      font-size: 0;
    }

    &__fakecheck {
      width: 18px;
      height: 18px;
      border: 2px solid @color-border;
      display: block;
      border-radius: 50%;
      position: relative;
      left: -10px;
      background: white;
      transform: scale(1, 1);
    }

    &__fakecheck ums-icon {
      width: 10px;
      height: 10px;
      color: @color-white;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
    }
    /* hover */
    &__container:hover * {
      border-color: @color-success !important;
      .transition();
    }

    &__container:hover tr td {
      background: lighten(@color-success, 58%) !important;
      .transition();
    }

    &__container:hover .ums-table__winnerselect__fakecheck {
      background: @color-success;
      transform: scale(1.2, 1.2);
      .transition();
    }
    /* checked */
    &__container.is-checked * {
      border-color: @color-success !important;
      .transition();
    }

    &__container.is-checked tr td {
      background: lighten(@color-success, 58%) !important;
      .transition();
    }

    &__container.is-checked .ums-table__winnerselect__fakecheck {
      background: @color-success;
      color: @color-white !important;
      transform: scale(1.2, 1.2);
      .transition();
    }

    &__container.is-checked .ums-table__winnerselect__status * {
      opacity: 1 !important;
      .transition;
    }
  }

  &__winner {
    background: @color-conversion;
    position: absolute;
    top: 0;
    bottom: 0;
    left: -15px;
    margin: auto 0;
    color: @color-white;
    border-radius: 50%;
    display: block;
    width: 30px;
    height: 30px;
    text-align: center;
  }

  &__winner ums-icon {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    width: 65% !important;
    height: 65% !important;
    text-align: center;
    justify-content: center;
  }
  // ANNOTATIONS
  &__annotation-icon {
    width: 14px;
    height: 14px;
    margin-right: 5px;
    position: relative;
    top: -1px;
  }

  &__annotation-timestamp-holder {
    display: flex;
    flex-direction: column;

    & > * {
      width: 100%;

      & + * {
        margin-top: 4px;
      }
    }
  }
  // OTHERS
  &__toggle {
    background: none;
    opacity: 0.25;
    border: 0;
    margin-top: 4px;
    margin-left: 10px;
  }

  &__search {
    position: relative;
    width: 160px;
    height: 30px;
    margin-top: 10px !important;
    margin-bottom: 0 !important;
    padding: 0 @m-small / 2 !important;
    .fz-md();
    font-weight: @fw-bold;
    border: 1px solid @color-border;
    border-radius: @br-base / 2 !important;
    appearance: none;

    & {
      background-position: right;
      background-size: 30px;
      background-repeat: no-repeat;
      background-image: url(data:image/svg+xml;base64,);
    }

    &:focus {
      width: 100%;
      .transition;
    }
  }

  &__buttonholder {
    display: flex;
    justify-content: flex-end;

    & > * + * {
      margin-left: 5px;
    }
    &--center {
      align-items: center;
    }
    &--m-large {
      & > * + * {
        margin-left: 10px;
      }
    }
  }

  &__drilldown-button {
    border: 0;
    background: transparent;
    padding: 0;
    font-family: inherit;
    text-align: left;
    color: @color-base;
    text-decoration: none;
    font-size: 13px;
    display: flex;
    align-items: center;
    flex: 1 1 auto;
    margin-top: -10px;
    margin-bottom: -10px;
    user-select: text;
    transition: color 0.2s ease;
    cursor: auto;

    &.s-drilldown {
      cursor: pointer;
      & strong {
        color: @color-main;
        text-decoration: underline;
        &:hover,
        &:focus {
          color: lighten(@color-main, 20);
        }
      }
    }
  }

  &__fullscreen-button {
    padding: 10px;
    border: 0;
    color: @color-main;
    display: flex;
    align-items: center;
    transition: color 0.2s ease;
    &:hover,
    &:focus {
      color: lighten(@color-main, 20);
    }
  }
  &__one-in-row {
    display: inline-flex;
    align-items: center;
  }
  &__sort-indicator {
    cursor: pointer;
    user-select: none;
    & > span {
      border-bottom: 1px solid gray;
    }
  }

  &__specs {
    &-holder {
      display: flex;
      flex-wrap: wrap;

      &--margin {
        margin-left: -12px;
        margin-bottom: -8px;
      }
    }
    &-item {
      background: #d9d9da;
      border-radius: 5px;
      padding: 10px 15px;
      margin-bottom: 8px;
      margin-right: 8px;
      position: relative;

      &--line:not(:last-child) {
        &::before {
          position: absolute;
          content: "";
          width: 8px;
          right: -8px;
          top: 0;
          bottom: 0;
          margin: auto;
          height: 3px;
          background: #d9d9da;
        }
      }

      &.s-active {
        background: @color-main;
        color: #fff;
        &:not(:last-of-type)::before {
          background: @color-main;
        }
      }
    }
  }
  &__flap-label {
    &-holder {
      display: flex;
      position: absolute;
      right: 0;
      left: auto;
      top: 0;
      bottom: 0;
    }

    &-item {
      height: 100%;
      display: flex;
      align-items: center;
    }

    &-text {
      border: 0 !important;
      border-left: 1px solid #d1d4dc !important;
      border-radius: 0 !important;
      background-color: transparent !important;
      font-size: 15px !important;
      color: fade(@color-gray, 65);
      font-weight: 600;
      width: auto;
      padding: 5px 30px;
      height: 100% !important;
      display: flex;
      align-items: center;
    }

    &-button {
      display: flex;
      text-decoration: none !important;
      & > *:not(:last-child) {
        margin-right: 10px;
      }
      &.s-active {
        color: @color-main;
      }
      &:hover {
        background: #f5f5f5 !important;
      }
    }

    &-badge {
      width: 12px;
      height: 12px;
      position: absolute;
      top: 11px;
      right: 18px;
      color: #fff;
      background-color: @color-main;
      border: 2px solid #fff;
      border-radius: 50%;
      font-size: 10px;
      font-weight: 700;
      padding: 2px;
      box-sizing: content-box;
      display: flex;
      align-items: center;
      justify-content: center;
      line-height: 1;
    }

    &-select {
      padding: 5px 47px 5px 30px;
      background-position: right 30px center;
      &[disabled] {
        background-color: transparent !important;
      }
    }
  }
}
