

// LEGENDA 

.ums-legenda {
		
	&__container 	{text-align: right;}

	&__list 		{list-style: none; padding: 0; margin: 0;}

	&__item 		{display: inline-block; margin-left: @m-small; padding-left: @m-small; position: relative;}
	
	&__bullet 		{position: absolute; top: 0; left: 0; bottom: 0; margin: auto 0; width: @m-small/2; height: @m-small/2; display: block; background: @color-light-c; border-radius: 50%;}

}     