// STARTPAGE

.ums-startpage {


    
	&__version {
		display: inline-flex;
		justify-content: flex-end;
		padding-right: 2px;
		transition: opacity 0.6s ease;

		&.s-invisible {
			opacity: 0;
		}
	}
}

.ums-startpage-intro {
	&__grid {
		display: grid;
		grid-template-areas: "title icon" "text icon" "tiles tiles";
        grid-template-columns: ~"minmax(0, 1fr) min(180px, 14vw)";
		grid-template-rows: auto auto auto;
		column-gap: 60px;
		row-gap: 20px;
		& > [class*="__title"] {
            grid-area: title;
        }
		& > [class*="__text"] {
            grid-area: text;
        }
		& > [class*="__bgimage"] {
            grid-area: icon;
        }
		& > [class*="__tiles"] {
            grid-area: tiles;
        }
	}
	&__title {
		margin: 0;
        font-size: 36px !important;
        line-height: 1.25;
        color: @color-main;
    }
	&__text {
		min-height: 100px;
	}
	&__tiles {
		/* autoprefixer grid: off */
        display: grid;
		grid-template-columns: repeat(auto-fill, minmax(210px, 1fr));
		grid-gap: 25px;
		list-style: none;
		padding: 0;
		margin: 0;
    }
}

.ums-startpage-channels {
	&__grid {
		/* autoprefixer grid: off */
        display: grid;
		grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
		grid-gap: 40px;
		list-style: none;
		padding: 0;
		margin: 0;
    }
	&__item {
		display: flex;
		flex-direction: column;
		& > * + * {
			margin-top: 10px;
		}
	}
}

.ums-startpage-tile {
    &__item {
        display: grid;
        grid-template-areas: "icon ." "title title" "subtext subtext";
        grid-template-columns: 70px minmax(0, 1fr);
		grid-template-rows: auto auto 1fr;
        column-gap: 0;
        row-gap: 2px;
        padding: 25px 40px;
		border-radius: 3px;
		transition: filter .2s ease, box-shadow .2s ease;
		height: 100%;

		& > * {
			margin: 0;
		}

		& > img {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			height: 100%;
			object-fit: cover;
		}

		& > [class*="__icon-holder"] {
			margin: 0 0 20px 0;

			& svg, & img {
				width: 70px;
				height: 70px;
				object-fit: contain;
			}
		}

		&.s-small {
			grid-template-areas: "icon title" "icon subtext";
			grid-template-columns: 30px minmax(0, 1fr);
			grid-template-rows: auto auto;
			column-gap: 20px;
			row-gap: 2px;
			padding: 20px 30px;

			& > [class*="__icon-holder"] {
				margin: 0;

				& svg, & img {
					width: 30px;
					height: 30px;
				}
			}
		}

		&[href] {
			&:hover, &:focus {
				box-shadow: 0 3px 2px rgba(0,0,0,0.15) !important;
				filter: brightness(0.96) saturate(1.2);
			}
		}
		
		&.s-gray {
			background: hsl(0, 0%, 95%);
			border-color: hsl(0, 0%, 95%);
			&[href] {
				&:hover, &:focus {
					filter: brightness(0.96) saturate(1.2);
				}
			}
		}

		&.s-alt {
			color: #fff;
			background: @color-graph07;
			border-color: @color-graph07;
			&[href] {
				&:hover, &:focus {
					filter: brightness(0.92) saturate(1.2);
				}
			}
			& > [class*="__arrow"] {
				background: rgba(0,0,0,0.15);
				color: #fff;
			}
		}
		&.s-transparent {
			color: #fff;
			background: transparent;
			border-color: transparent;
			& > [class*="__arrow"] {
				background: rgba(0,0,0,0.15);
				color: #fff;
			}
		}

        & > [class*="__icon-holder"] {
            grid-area: icon;
        }

        & > [class*="__title"] {
            grid-area: title;
            align-self: end;
        }

        & > [class*="__subtext"] {
            grid-area: subtext;
            align-self: start;
        }
    }

    &__title {
        font-size: 20px !important;
		line-height: 1.1;
    }
    
	&__icon-holder {
		display: flex;
		flex-direction: column;
		justify-content: center;
    }
	&__arrow {
		position: absolute;
		top: 0;
		right: 0;
		width: 30px;
		height: 30px;
		background: rgba(0,0,0,0.05);
		color: rgba(0,0,0,0.25);
		border-radius: 0 3px 0 3px;
		display: flex;
		align-items: center;
		justify-content: center;
		z-index: 2;
	}
}
