

// TEST DETAIL

.ums-testdetail 			{

	&__container 			{text-decoration: none; box-shadow: @bs-base; background: @color-white; border: 1px solid @color-border; display: flex; flex-flow: row wrap; position: relative; overflow: hidden; border-radius: @br-base; margin-top: @m-base/1.5; transform: scale(1,1) translateZ(0); backface-visibility: hidden;  .transition;}

	&__container:hover 		{box-shadow: @bs-hover; .transition;}

	@media @min1600 	{

		&__container 		{min-height: 205px;}

	}

	/* &__container:hover  .ums-testdetail__sum__background svg 	{transform:rotate(67.5deg); .transition(.3s, ease-out);} */

	&__container:hover  .ums-testdetail__sum__background svg 	{transform: translateX(10px) rotate(22.5deg); .transition;}

	&__sum 					{

		&__container 		{flex: 1; max-width: 32%; padding: @m-small @m-base @m-small @m-base/1.5; position: relative; z-index: 2;

			.umsLabel 		{opacity: .75; font-weight: @fw-heavy;}

			* 	 			{color: @color-white !important;}}

		&__background 		{color: @color-success; position: absolute; top: -9999px; bottom: -9999px; margin: auto 0; left: -28%; width: 60%; height: 0; padding-bottom: 60%; z-index: 1; transform: translateY(-11%);

			svg 			{position: absolute; top: 0; left: 0; fill: @color-black; fill: currentColor; width: 100%; height: 100%; transform: translateX(0px) rotate(22.5deg); .transition;}}

		&__title 			{color: @color-white; display: flex; flex-flow: row wrap; align-items: center;}

		&__icon 			{color: @color-white; margin-right: 10px; width: 25px; height: 25px;}

		&__info 			{margin: 1rem 0 0;}

		@media @min1600 		{

			&__container 	{padding: @m-base @m-large @m-base @m-large/1.5;}

		}

	}


	&__badge 				{

		&__container 		{position: absolute; text-align: center; padding: 0; top: 0; bottom: 0; margin: auto 0; left: 25%;  width: 120px; height: 120px; z-index: 2; color: @color-white;}

		&__icon 			{display: inline-block; width: 25px; height: 25px; svg {width: 100%; height: 100%;}}

		&__winner 			{font-size: 33px; font-weight: 900; margin-left: 6px; display: inline-block; position: relative; top: 2px; left: -3px; line-height: 1;}

		&__title 			{display: block; width: 100%; line-height: 1.2; .fz-md(); font-weight: @fw-heavy;}

		&__background 		{position: absolute; top: 0; left: 0; width: 100%; height: 100%; z-index: -1; fill: darken(@color-success,5%);}

		&__container--winner .ums-testdetail__badge__background {fill: @color-conversion;}

		&__popup-holder 	{padding: 36px 0 0; height: 100%;
			& > .ums-popup__holder {
				transform: translateY(0px);
			}
		}

	}


	&__info 				{

		&__container 		{flex: 1; max-width: 66%; padding: @m-small @m-base @m-small @m-large;}

		&__title 			{.fz-lg(); margin-bottom: 1rem; font-weight: @fw-bold;}

		@media @min1600 		{

			&__container 	{ padding: @m-base @m-large;}

			&__title 		{font-size: 20px !important;}

		}

	}


	&__empty 				{

		&__container 		{max-width: 700px; margin: 0 auto; text-align: center;}

		&__container img	{max-width: 100%; width: 600px;}

	}



	/* states */

	&__container--green .ums-testdetail__info__title 		{color: @color-success !important;}
	&__container--green	.ums-testdetail__sum__background 	{color: @color-success !important;}

	&__container--gray 	.ums-testdetail__info__title 		{color: @color-gray !important;}
	&__container--gray 	.ums-testdetail__sum__background 	{color: @color-gray !important;}

	&__container--blue 	.ums-testdetail__info__title 		{color: @color-main !important;}
	&__container--blue 	.ums-testdetail__sum__background 	{color: @color-main !important;}

	&__container--orange .ums-testdetail__info__title 		{color: @color-warning !important;}
	&__container--orange .ums-testdetail__sum__background 	{color: @color-warning !important;}

}
