.ums-profile {
  &__side-sticky {
    position: sticky;
    top: 40px;
  }

  &__target-table {
    tbody + tbody {
      position: relative;

      & > tr:first-of-type > td {
        padding-top: 40px;

        & > span::before {
          content: "";
          border-top: 1px solid #d1d4dc;
          position: absolute;
          top: 18px;
          left: 0;
          right: 0;
        }
      }
    }
  }

  &__image-holder {
    position: relative;
    width: 100%;
    height: 0;
    padding-top: 100%;
    overflow: hidden;
    border-radius: 5px;
  }

  &__image {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    object-fit: cover;
    object-position: center;
    height: 100%;
    width: 100%;
  }

  &__table-type-holder {
    width: 78px !important;
    min-width: 0;

    &--full {
      width: 100px !important;
    }

    &--small {
      width: 22px !important;
    }
  }

  &__type-holder {
    display: flex;
    align-items: center;
    margin: -15px -5px -15px -2px;
    width: 100%;
    z-index: 2;
    position: relative;

    & > * + * {
      margin-left: 15px;
      margin-top: 0px;
    }
  }

  &__type-indicator {
    width: 17px;
    height: 17px;
    border-radius: 50%;
    display: flex;
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    background: fade(@color-gray, 15%);
    border: 3px solid #fff;
    color: @color-gray;
    flex: 0 0 auto;
    margin-top: 6px;
    margin-left: 9px;
    left: 16px;
    position: relative;

    &::after {
      content: "";
      z-index: -1;
      background: #fff;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      border-radius: 50%;
    }

    &--left {
      left: 0;
    }

    &.s-active {
      background: @color-main;
      color: #fff;
    }

    &.s-active-light {
      background: fade(@color-main, 20);
      color: @color-main;
    }

    &.s-large {
      width: 34px;
      height: 34px;
      border: 0;
      margin-left: 0;
      margin-top: 0;
    }

    &.s-icon {
      width: 26px;
      height: 26px;
      margin-left: 4px;
      margin-top: 0px;
    }

    &.s-small {
      width: 15px;
      height: 15px;
      margin-left: 0;
      margin-top: 0;
    }

    &--second {
      left: 3px;
      top: 7px;
    }

    & > *:not(.ums-popup__holder) {
      width: 50%;
      height: 50%;
    }
  }

  &__line-parent {
    position: relative;
    width: 90px;

    &--bottom {
      & > span[class*="__line-item"] {
        height: calc(50% - 20px);
        top: auto;
        bottom: 0;
      }
    }

    &--top {
      & > span[class*="__line-item"] {
        top: 0;
        bottom: auto;
        height: calc(50% - 17px);
      }
    }

    &--corner {
      & > span[class*="__line-item"] {
        top: 0;
        bottom: auto;
        height: 50px;
        width: 34px;

        &::after {
          border-bottom: 3px solid #d9d9da;
        }
      }
    }
  }

  &__line-item {
    position: absolute !important;
    top: auto;
    bottom: 0;
    height: 100%;
    z-index: 1 !important;
    left: 60px;
    opacity: 0;
    display: flex;
    flex-direction: column;

    &::before,
    &::after {
      content: "";
      border-left: 3px solid #d9d9da;
      flex: 1;
    }

    &--rotated {
      transform: rotate(-45deg);
      transform-origin: bottom right;
      height: 46px;
      &.s-session-active:not(.s-active-before)::before {
        border: 0;
        background: linear-gradient(@color-main 30%, #d9d9da);
      }
    }

    &--inside {
      left: 15px;
    }

    &.s-active-before::before {
      border-color: @color-main !important;
    }

    &.s-active-after::after {
      border-color: @color-main !important;
    }

    &.s-dashed-before::before {
      border: 0;
      width: 3px;
      background-image: repeating-linear-gradient(0deg, #d9d9da, #d9d9da 4px, transparent 4px, transparent 8px, #d9d9da 8px), repeating-linear-gradient(90deg, #d9d9da, #d9d9da 4px, transparent 4px, transparent 8px, #d9d9da 8px), repeating-linear-gradient(180deg, #d9d9da, #d9d9da 4px, transparent 4px, transparent 8px, #d9d9da 8px), repeating-linear-gradient(270deg, #d9d9da, #d9d9da 4px, transparent 4px, transparent 8px, #d9d9da 8px);
      background-size: 3px 100%, 100% 0, 0 100%, 100% 0;
      background-position: 0 5px, 0 0, 100% 0, 0 100%;
      background-repeat: no-repeat;
    }

    &.s-dashed-after::after {
      border: 0;
      width: 3px;
      background-image: repeating-linear-gradient(0deg, #d9d9da, #d9d9da 4px, transparent 4px, transparent 8px, #d9d9da 8px), repeating-linear-gradient(90deg, #d9d9da, #d9d9da 4px, transparent 4px, transparent 8px, #d9d9da 8px), repeating-linear-gradient(180deg, #d9d9da, #d9d9da 4px, transparent 4px, transparent 8px, #d9d9da 8px), repeating-linear-gradient(270deg, #d9d9da, #d9d9da 4px, transparent 4px, transparent 8px, #d9d9da 8px);
      background-size: 3px 100%, 100% 0, 0 100%, 100% 0;
      background-position: 0 0px, 0 0, 100% 0, 0 100%;
      background-repeat: no-repeat;
    }
  }

  &__indicators-holder {
    display: flex;
    gap: 6px;
    flex-flow: row wrap;
    margin: -10px 0;

    & > * {
      height: 34px;
    }
  }

  &__conversion-indicator, &__regular-indicator {
    background: @color-conversion;
    color: #fff;
    border-radius: 100px;
    padding: 7px 20px;
    margin: 0;
    font-weight: 600;
    display: inline-flex;
    align-items: center;
    height: 34px;

    &[href]:hover {
      text-decoration: none;
      background: darken(@color-conversion, 10);
    }

    & > span,
    & .ums-icon-wrap > span {
      top: 1px;
      position: relative;
      max-width: 200px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }

    & .ums-icon {
      top: -2px !important;
      position: relative;
    }
  }
  &__regular-indicator {
    background: fade(@color-light-a, 80%);
    color: #000;

    &[href]:hover {
      background: @color-light-a;
    }
  }

  &__variants-row-holder {
    display: flex;
    flex-flow: row wrap;
    gap: 5px 10px;
    max-width: 200px;
    z-index: 1;
    position: relative;
  }
}

.ums-table__tr.s-open .ums-profile__line-parent > span[class*="__line-item"],
.ums-table__tr.s-open ~ tr .ums-profile__line-parent > span[class*="__line-item"] {
  opacity: 1;
}

.ums-profile-related {
  &__member-grid {
    display: grid;
    grid-template-areas: "umsPrIdent umsPrName umsPrName" "umsPrIdent umsPrLogin umsPrButton";
    grid-template-columns: 30px 1fr auto;
    grid-template-rows: auto auto;
    column-gap: 15px;
    row-gap: 5px;

    & > [class*="__ident"] {
      grid-area: umsPrIdent;
    }

    & > [class*="__name"] {
      grid-area: umsPrName;
    }

    & > [class*="__login"] {
      grid-area: umsPrLogin;
    }

    & > [class*="__button"] {
      grid-area: umsPrButton;
      justify-self: end;
      align-self: end;
    }
  }
  &__profile-grid {
    display: grid;
    grid-template-areas: "umsPrSpecs umsPrSpecs" "umsPrSessions umsPrButton";
    grid-template-columns: 1fr auto;
    grid-template-rows: auto auto;
    column-gap: 15px;
    row-gap: 5px;

    & > [class*="__specs"] {
      grid-area: umsPrSpecs;
    }

    & > [class*="__sessions"] {
      grid-area: umsPrSessions;
      display: flex;
      flex-direction: column;

      & > * + * {
        margin-top: 10px;
      }
    }

    & > [class*="__button"] {
      grid-area: umsPrButton;
      justify-self: end;
      align-self: end;
    }
  }
}

.ums-profile-user {
  &__container {
    display: flex;
    width: 30px;
    height: 30px;
    border-radius: 3px;
    overflow: hidden;
    position: relative;
  }

  &__avatar {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    object-fit: cover;
    width: 100%;
    height: 100%;
  }

  &__initials {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: @color-main;
    color: #fff;
    font-weight: 600;
    text-transform: uppercase;
  }
}

.ums-profile-filter {
  &__tiny-input-holder.ums-filter__row {
    width: 170px;
    max-width: none;
    flex: 0 0 auto;
    & input.ums-form__input {
      padding-left: 9px !important;
      padding-right: 9px !important;
    }
    & .ums-form__input-iconwrap input.ums-form__input {
      padding-left: 55px !important;
    }

    & .ums-form__input-icon {
      font-size: 11px !important;
      padding: 6px;
    }
  }
  
  &__checkbox-row.ums-filter__row {
    max-width: none;
    width: calc(100% - 340px);
  }

  &__label {
    display: block;
    width: 100%;
    font-size: 11px !important;
    font-weight: 700;
    opacity: 0.35;
    text-align: left;
  }

  &__datepicker .ums-datepicker__wrapper::before {
    display: none;
  }
}
