// VARIABLE (nodig in datepicker, kon niet anders)

@inputStyles: {
  .fz-md();
  background: @color-white;
  color: @color-base;
  width: 100%;
  max-width: 100%;
  padding: @m-small / 2 + 1px @m-base / 2;
  height: auto !important;
  border: 1px solid @color-light-c !important;
  border-radius: @br-base / 2 !important;
  appearance: none;
  margin: 0;
  line-height: 1.2;
  &:focus {
    background-color: @color-white !important;
    .transition;
  }
};

@inputStylesDark: {
  @inputStyles();
  background-color: fade(@color-white, 5%) !important;
  border-color: fade(@color-white, 10%) !important;
  color: @color-white !important;
  &:focus {
    background-color: fade(@color-white, 5%) !important;
    .transition;
  }
};

@inputSelect: {
  padding-right: @m-base;
  background-size: 8px 8px;
  background-position: right @m-base / 2 center;
  background-repeat: no-repeat;
  background-image: url('data:image/svg+xml;utf8;base64,');
};

@inputSelectDark: {
  @inputSelect();
  background-image: url('data:image/svg+xml;base64,') !important;
};

// FORMS

.ums-form {
  // ROWS

  &__row {
    width: 100%;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
  }

  &__row + .ums-form__row {
    margin-top: @m-small;
  }

  &__row--small + .ums-form__row {
    margin-top: 6px;
  }

  &__subrow {
    width: ~'calc(50% -' @m-base / 2 ~')';
    > * {
      min-width: 100% !important;
      max-width: none;
    }
  }

  &__row input::placeholder {
    color: #a9a9a9;
  }

  &__row ums-render-property {
    width: 100%;
  }

  &__row .umb-property-editor--preview {
    color: red !important;
  }

  &__array {
    display: flex;
    width: 100%;
    flex-direction: row;
    flex-wrap: wrap;
    list-style: none;
    margin: 0;
    padding: 0;
    & > * {
      margin-right: 4px !important;
      margin-bottom: 4px !important;
    }
  }

  &__grid-layout {
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    grid-column-gap: 40px;
    grid-row-gap: 20px;
    & > * {
      margin: 0 !important;
    }
    &--col-1 {
      grid-column-start: 1;
    }
    &--col-2 {
      grid-column-start: 2;
    }
    &--col-span-2 {
      grid-column-start: 1;
      grid-column-end: span 2;
    }
    &--row-span {
      grid-row-end: span 2;
      grid-row-start: 1;
      display: flex;
      flex-direction: column;
      & > label {
        flex: 0 0 auto;
      }
      & > label + * {
        flex: 1 1 auto;
      }
    }
  }

  // INPUT

  &__input:not([type='radio']):not([type='checkbox']) {
    @inputStyles();
  }

  &__input--dark:not([type='radio']):not([type='checkbox']) {
    @inputStylesDark();
  }

  &__input--flex {
    flex: 1 1 0;
  }

  &__input--flex ~ button {
    margin-left: 10px;
  }

  &__input:disabled,
  &__input--disabled {
    color: fade(@color-gray, 75%) !important;
    pointer-events: none;
    background: fade(@color-light-a, 50%) !important;
    cursor: disabled !important;
  }

  &__input--auto:not([type='radio']):not([type='checkbox']) {
    width: auto !important;
  }

  &__input--filter:not([type='radio']):not([type='checkbox']) {
    border-radius: 1.7rem !important;
    padding-top: 4px;
    padding-bottom: 4px;
    margin-top: 6px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__input--empty:not([type='radio']):not([type='checkbox']) {
    background: transparent !important;
    border: 0 !important;
    padding-left: 0;
    padding-right: 0;
  }

  // SELECT

  &__select {
    @inputStyles();
    @inputSelect();

    &--dark {
      @inputStylesDark();
      @inputSelectDark();
      option {
        color: @color-base;
        @media (prefers-color-scheme: dark) and (min--moz-device-pixel-ratio:0) {
          color: #fff;
        }
      }
    }

    &--flex {
      flex: 1 1 0;
    }

    &--flex ~ button {
      margin-left: 10px;
    }

    &:disabled {
      background-color: fade(@color-light-a, 50%) !important;
      color: #828282 !important;
    }
  }

  // LABEL

  &__label {
    margin-bottom: @m-small / 3;
    width: 100%;
    .fz-md();
    font-weight: @fw-bold;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    position: relative;

    &--dark {
      color: @color-white !important;
    }
    &--light {
      font-size: 11px !important;
      margin-bottom: 1px;
      line-height: 1.7;
      opacity: 0.35;
    }
  }

  &__textitem {
    margin: 0;
  }

  // INFO BULLET

  &__info {
    &__bullet {
      background: fade(@color-border, 0%);
      z-index: 10;
      cursor: pointer;
      width: 25px;
      height: 25px;
      padding: 5px;
      display: inline-flex;
      border-radius: @br-base / 2;
      margin-top: -2px;
      .transition;
    }

    &__icon {
      width: 15px;
      height: 15px;
      color: @color-gray;
      opacity: 0.5;
    }

    &__text {
      display: none;
      background: @color-base;
      color: @color-white;
      position: absolute;
      top: auto;
      bottom: 30px;
      left: auto;
      right: 0;
      max-width: 100%;
      padding: 15px 20px;
      border-radius: @br-base;
      box-shadow: @bs-base;
    }

    &__text--wide {
      width: 500px;
      max-width: 500px;
    }

    &__text:after {
      content: '';
      position: absolute;
      top: auto;
      bottom: -5px;
      width: 18px;
      height: 18px;
      display: block;
      left: auto;
      right: 3px;
      background: @color-base;
      transform: rotate(45deg);
      border-radius: @br-base / 2;
    }

    &__text--left {
      left: 0;
      right: auto;
      &:after {
        left: 3px;
        right: 0;
      }
    }

    &__bullet:hover {
      background: fade(@color-border, 75%);
      .transition;
    }

    &__bullet:hover .ums-form__info__text {
      display: block;
    }

    &__bullet:hover .ums-form__info__icon {
      opacity: 1;
    }
  }

  // ICON

  &__input-iconwrap {
    position: relative;
    display: flex;
    flex-flow: row wrap;
    flex: 1 1 0;
    justify-content: space-between;

    &:not([class*='iconwrap--auto']) {
      input,
      select,
      > div {
        padding-left: @m-small / 2 + 54px !important;
      }
    }

    umb-flatpickr {
      width: 100%;
    }

    &--auto {
      [class*='-form__input-icon'] {
        position: relative;
        border: 1px solid @color-light-c !important;
        border-right: 0 !important;
        &:first-child {
          border-radius: @br-base / 2 0 0 @br-base / 2 !important;
        }
        &:last-child {
          border-radius: 0 @br-base / 2 @br-base / 2 0 !important;
        }
      }
      input,
      input:not([type='radio']):not([type='checkbox']),
      select,
      select:not([type='radio']):not([type='checkbox']) {
        flex: 1 1 0;
        max-width: ~'calc(100% - 40px)';
        &:first-child {
          border-radius: @br-base / 2 0 0 @br-base / 2 !important;
          border-right: 0 !important;
        }
        &:last-child {
          border-radius: 0 @br-base / 2 @br-base / 2 0 !important;
        }
      }
    }
  }

  &__innerrow {
    display: flex;
    flex-flow: row wrap;
    width: 100%;
    margin-top: 5px;
    justify-content: space-between;

    .btn {
      width: 80px;
      .fz-md;
      font-weight: @fw-bold;
      color: @color-base;
      background-color: @color-light-a;
      border: 1px solid @color-light-c;
      margin-left: 5px;
      text-align: center;
      .transition;
    }

    .btn:hover {
      background-color: @color-light-b;
    }
  }

  &__innerrow-special {
    display: flex;
    width: 100%;
    &__button {
      padding-left: 20px;
      padding-right: 20px;

      &--grow {
        flex: 1 0 auto;
        display: flex;
        & > span {
          flex: 1 0 auto;
          text-align: left;
        }
      }
    }
    & > * + *,
    & > .btn + .btn {
      margin-left: 5px;
    }
  }

  &__input-icon {
    width: 40px;
    background-color: @color-light-a;
    border: 1px solid @color-light-c;
    border-radius: @br-base / 2 0 0 @br-base / 2;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    color: @color-black !important;
    .fz-md();
    font-weight: bold;
    margin: 0;
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: center;
    &--auto {
      padding: 0 9px;
      height: auto;
    }

    &--noflex {
      position: relative;
      width: auto;
      display: inline-flex;
      height: auto;
      padding-left: 9px;
      order: -1;
      padding-right: 9px;
      white-space: nowrap;
    }

    &--color-dark {
      color: @color-base;
    }
  }

  &__input-icon--dark {
    background-color: fade(@color-white, 5%);
    border-color: fade(@color-white, 10%);
    color: @color-white !important;
  }

  // TOGGLE

  &__toggle {
    display: flex;
    flex-flow: row;
    align-items: center;
    cursor: pointer;
    & > * {
      margin-bottom: 0;
    }

    &[disabled='disabled'] {
      pointer-events: none;
    }

    &__input {
      @vh();
    }

    &__label {
      position: relative;
      font-weight: @fw-heavy;
      color: fade(@color-gray, 50%);
      padding-right: 20px;
      .transition;

      &:before {
        content: '';
        position: absolute;
        left: auto;
        right: 0;
        top: 0;
        bottom: 0;
        margin: auto 0;
        width: 12px;
        height: 12px;
        background: @color-gray;
        border-radius: 12px;
        opacity: 0;
        z-index: @z-content;
      }

      &:after {
        content: '';
        position: absolute;
        left: auto;
        right: -12px;
        top: 0;
        bottom: 0;
        margin: auto 0;
        width: 24px;
        height: 8px;
        background: @color-light-c;
        border-radius: 12px;
      }

      &:hover {
        color: @color-gray;
      }
    }

    &__label:nth-of-type(2) {
      padding-right: 0;
      padding-left: 20px;

      &:before {
        left: 0;
        right: auto;
      }

      &:after {
        display: none;
      }
    }

    &__input:checked + .ums-form__toggle__label {
      color: @color-gray;

      &:before {
        opacity: 1;
      }

      &--yes {
        color: @color-success;
        &:before {
          background: @color-success;
        }
      }

      &--no {
        color: @color-error;
        &:before {
          background: @color-error;
        }
      }
    }

    &__input[type='checkbox'] + .ums-form__toggle__label:before {
      opacity: 1;
      left: -15px;
      background: @color-main;
      color: @color-main;
      .transition;
    }
    &__input[type='checkbox'] + .ums-form__toggle__label {
      color: @color-main;
    }
    &__input[type='checkbox'] + .ums-form__toggle__label--archived {
      &:nth-of-type(2):before {
        left: 0 !important;
      }

      & {
        color: @color-gray;
      }

      &:before {
        background: @color-gray;
      }
    }

    .s-toggleByAngular {
      pointer-events: none;
    }

    /* datepicker */

    &--datepicker {
      margin: 0;
      width: 100%;
      padding: 0;
      border: 0;
    }

    &--datepicker .ums-form__toggle__label {
      padding-right: 10px !important;
    }

    &--datepicker .ums-form__toggle__input[type='checkbox'] + .ums-form__toggle__label {
      color: @color-white;
    }
    &--datepicker .ums-form__toggle__input[type='checkbox'] + .ums-form__toggle__label:before {
      background: @color-white;
      color: @color-white;
      left: -15px;
    }
    &--datepicker
      .ums-form__toggle__input[type='checkbox']
      + .ums-form__toggle__label:nth-of-type(2) {
      padding-left: 27px;
    }

    &--datepicker .ums-form__toggle__label:hover,
    &--datepicker .ums-form__toggle__input:hover {
      color: red;
    }

    &--datepicker .ums-form__toggle__input[type='checkbox']:checked + .ums-form__toggle__label {
      color: @color-conversion;
    }
    &--datepicker
      .ums-form__toggle__input[type='checkbox']:checked
      + .ums-form__toggle__label:before {
      left: 3px;
      background: @color-conversion;
      color: @color-conversion;
    }

    &--datepicker .ums-form__toggle__label:after {
      opacity: 0.5;
    }
  }

  // SWITCH (RADIO)

  &__switch {
    display: flex;
    flex-flow: row;
    align-items: center;
    background: @color-light-b;
    border-radius: @br-base / 2 !important;
    padding: 2px;
    border: 1px solid @color-border;
    width: 100%;
    max-width: 320px;
    max-height: 39px;

    &__input {
      @vh();
    }

    &__label {
      @inputStyles();
      max-height: 35px;
      border: 0 !important;
      background: none;
      color: @color-gray !important;
      font-weight: @fw-bold;
      text-align: center;
      border-radius: @br-base / 2 !important;
      width: auto;
      flex: 1 1 0;
      .transition;

      &--lg {
        padding: 10px;
        max-height: none;

        @media @min1600 {
          padding: 12px 20px;
          min-width: 120px;
        }
      }
      &--thin {
        padding-top: 9px;
        padding-bottom: 8px;
      }

      & + input + .ums-form__switch__label {
        margin-left: 4px;
      }

      &:hover {
        box-shadow: @bs-base;
        background: @color-base !important;
        z-index: 1;
        color: @color-white !important;
      }
    }

    &__input:checked + .ums-form__switch__label {
      color: @color-white !important;
      background: @color-main;
      box-shadow: @bs-hover;
    }

    &__input:checked + .ums-form__switch__label--success {
      color: @color-white !important;
      background: @color-success;
    }

    &__input:checked + .ums-form__switch__label--error {
      color: @color-white !important;
      background: @color-error;
    }

    &__input:disabled:checked + .ums-form__switch__label {
      color: @color-white !important;
      background: @color-gray;
      box-shadow: @bs-hover;
    }

    &__input:disabled + .ums-form__switch__label {
      pointer-events: none;
    }

    &--autosize {
      max-width: none;
      max-height: none;

      & > [class*='__switch__label'] {
        flex: 1 0 auto;
      }
    }
  }

  // DOCTYPE PICKER

  &__doctypeLabel {
    width: 100%;
    margin: 0;
  }

  &__doctypePicker {
    @inputStyles();
    display: block !important;
    margin: 5px 0 0;
    background: fade(@color-light-c, 25%);
    .transition;
  }

  &__doctypePicker:hover {
    background: fade(@color-light-c, 50%);
    cursor: pointer;
  }

  &__doctypePicker * {
    margin: 0 5px 0 0;
    position: relative;
    top: -3px;
  }

  // CHECKBOX

  &__checkbox {
    &__label {
      background: @color-white;
      border: 1px solid @color-border;
      line-height: 36px;
      border-radius: @br-base / 2;
      padding: 0 15px;
      height: 39px;
      line-height: 39px;
      display: flex;
      flex-flow: row wrap;
      align-items: center;
      .fz-md();
      margin-bottom: 0;
      > * {
        margin: 0 8px 0 0;
      }
    }

    &__label + .ums-form__checkbox__label {
      margin-left: @m-small / 3;
    }
    &__label--error {
      background: @color-error;
      color: #fff;
    }
    &__label--base {
      background: @color-base;
      color: #fff;
    }
    &__label--hover {
      cursor: pointer;
      &:not(.s-disabled) {
        &:hover {
          background: lighten(@color-light-a, 3%);
        }
      }
    }
    &__input {
      background-color: @color-white;
      border: 1px solid @color-light-c;
      border-radius: @br-base / 2;
      width: 16px !important;
      height: 16px !important;
      appearance: none;

      &:focus {
        border-color: @color-border;
      }

      &:checked {
        background-color: fade(@color-border, 50%);
        border-color: @color-border;
        background-repeat: no-repeat;
        background-size: 8px;
        background-position: center;
        background-image: url(data:image/svg+xml;base64,);
      }

      &--dark {
        background-color: fade(@color-white, 15%);
        border: 1px solid fade(@color-white, 25%);

        &:focus {
          border-color: @color-main;
        }

        &:checked {
          background-color: @color-main;
          background-image: url(data:image/svg+xml;base64,);
        }
      }
    }
  }

  &__box {
    position: relative;
    display: flex;

    &__input {
      position: absolute;
      top: 0;
      left: 0;
      width: 0px;
      height: 0px;
      opacity: 0;
    }

    &__label {
      display: flex;
      &::before {
        content: '';
        display: block;
        width: 14px;
        height: 14px;
        background: #fff;
        border: 1px solid #d1d4dc;
        border-radius: 3px;
        overflow: hidden;
      }
      &::after {
        content: '';
        display: block;
        width: 14px;
        height: 14px;
        border-left: 1px solid #d1d4dc;
        border-right: 1px solid #d1d4dc;
        border-radius: 3px;
      }
    }

    input:checked + label::before {
      // background: blue;
    }
  }

  // SCROLLER (needed for doctype selector in create Test)

  &__scroller {
    overflow-y: auto;
    width: 100%;
    max-height: 205px;
    position: relative;
    scroll-behavior: smooth;
    &:after {
      content: '';
      position: sticky;
      top: auto;
      bottom: 0;
      height: 20px;
      opacity: 1;
      display: block;
      background: linear-gradient(0deg, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 0) 100%);
      width: 100%;
      z-index: 100;
    }
  }
}

// Red borders on firefox fix:

input:required,
textarea {
  box-shadow: none !important;
}

// ERRORS & MESSAGES

@errorText: {
  .fz-sm();
  width: 100%;
  color: @color-error;
};
@errorField: {
  border-color: @color-error !important;
  background-repeat: no-repeat;
  background-position: top 12px right 12px;
  background-size: 12px;
  background-image: url('data:image/svg+xml;base64,');
};

.s-checkform .ums-form__select,
.s-checkform .ums-form__input:not([type='radio']):not([type='check']) {
  &.ng-empty:not(.ng-valid),
  &.ng-invalid {
    @errorField();
  }
}

.ums-errorText {
  @errorText();
}

// Umbraco Overwrites

.ums-form__row .umb-node-preview-add {
  margin-top: 5px;
  border-radius: @br-base / 2;
  min-width: 200px;
  width: 100%;
  .fz-md();
}

.ums-form__row .umb-node-preview {
  border: 1px solid @color-light-c;
  border-radius: @br-base / 2;
  margin: 5px 0 0;
  padding: 5px 10px 5px 5px;
  background: @color-light-a;
}

.ums-form__row .umb-node-preview__icon {
  margin-top: 5px;
  margin-left: 5px;
}

.datepicker-umbraco {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
}

.datepicker-umbraco .input-append {
  margin: 0;
  height: 42px;
  position: relative;
  display: flex;
  flex-flow: row wrap;
  flex: 1 1 0;
  justify-content: space-between;
}

.datepicker-umbraco input {
  @inputStyles();
  min-width: 200px;
  margin-right: 10px;
}

.datepicker-umbraco-flatpickr input {
  height: 100% !important;
  padding-top: @m-small / 2 + 3px;
}

.datepicker-umbraco .add-on {
  width: 40px;
  background-color: @color-light-a;
  border: 1px solid @color-light-c;
  border-radius: @br-base / 2 0 0 @br-base / 2;
  position: absolute;
  top: 0;
  left: -39px;
  height: 100%;
  color: @color-black !important;
  .fz-md();
  font-weight: bold;
  margin: 0;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: center;
}

.datepicker-umbraco .add-on * {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: center;
}

umb-date-time-picker .flatpickr-input {
  position: relative;
  display: flex;
  flex-flow: row wrap;
  height: 40px;
}

umb-date-time-picker input {
  order: 2;
  @inputStyles();
  flex: 1 1 0;
  border-radius: 0 @br-base / 2 @br-base / 2 0 !important;
  margin-right: 5px;
}

umb-date-time-picker .input-append .on-top {
  order: 3;
  border-radius: @br-base / 2;
  border: 1px solid @color-border;
  display: flex;
  flex-flow: row wrap;
  width: 40px;
  height: 40px;
  position: relative;
  margin: 0;
  padding: 0;
  top: 0;
  justify-content: center;
  align-items: center;
}

umb-date-time-picker .add-on {
  display: none !important;
}

ums-render-property + .ums-form__input-icon {
  z-index: 2;
}

// RANGE SLIDER

.rzslider {
  margin: 25px 0 15px 0;

  &:before {
    content: '';
    position: absolute;
    top: -30px;
    left: 0;
    width: 100%;
    height: 30px;
    background: linear-gradient(0deg, rgba(0, 0, 0, 1) 0%, rgba(255, 255, 255, 1) 100%);
    display: block;
    opacity: 0.05;
  }

  .rz-pointer {
    top: -8px;
    width: 20px;
    height: 20px;
    cursor: pointer;
    background-color: @color-base;
    border: 2px solid @color-white;

    &:after {
      content: '';
      position: absolute;
      top: -13px;
      width: 10px;
      height: 10px;
      left: 0;
      right: 0;
      margin: 0 auto;
      display: block;
      background: @color-base;
      transform: rotate(45deg);
      border-radius: 3px;
      z-index: -1;
    }
  }

  .rz-limit {
    visibility: visible !important;
    top: 8px;
    font-weight: @fw-bold;
    .fz-sm();
    opacity: 0.25;
    color: @color-base;
  }

  .rz-floor {
    visibility: visible !important;
  }

  .rz-bar {
    height: 6px;
    border-radius: 3px;
  }

  .rz-model-value {
    border-radius: 3px;
    background: @color-base;
    color: @color-white;
    .fz-sm();
    padding: 0px 5px;
    font-weight: @fw-bold;
    z-index: 10;
  }

  .rz-selection {
    background: @color-main;
  }

  .rz-restricted {
    display: none !important;
  }

  &[disabled='disabled'] .rz-selection {
    background: @color-gray !important;
  }

  &[disabled='disabled'] .rz-pointer {
    background: @color-gray !important;
  }
}
