

// DIVIDER

.ums-divider {

	&__container 	{display: flex; flex-flow: row; align-items: center; width: 100%; margin: @m-large 0 -@m-small/2; min-height: 35px;
		&--small-margin {margin-top: @m-small;}
	}

	&__title 		{display: inline-flex; color: @color-gray; align-items: center; font-weight: @fw-bold; .fz-md();

		ums-icon	{margin-right: @m-small/2;}}

	&__line 		{flex: 1; width: 100%; height: 1px; margin-left: @m-small; background: @color-border;}

}