

// A/B Testing 

.ums-testing {

	/* PROJECTS */

	&__project		{display: flex; flex-flow: row wrap; .fz-md(); width: 100%; color: @color-base;

		&__row 						{flex: 1 1 0; padding-left: @m-small; max-width: 15%; width: 15%;}

		&__row:nth-of-type(1)  		{padding-left: 0;}

		&__row:nth-of-type(2)  		{max-width: 35%; width: 35%;}

		&__row:nth-last-of-type(1) 	{max-width: 20%; width: 20%; text-align: right;}

		.ums-button		{margin-top: 30px;}

		img 			{width: 100%; margin-top: 5px; border: 1px solid @color-light-a;}
		
	}	
	
}