

// ICON

ums-icon 			{display: inline-flex; flex-flow: row wrap; align-items: center;}

.ums-icon  			{height: 100%; width: 100%; display: inline-block; fill: @color-black; fill: currentColor; display: inline-block; vertical-align: -0.1em;

	&--xxs 			{width: 10px; height: 10px;}

	&--xs 			{width: 12px; height: 12px;}

	&--sm 			{width: 14px; height: 14px;}

	&--md 			{width: 18px; height: 18px;}

	&--lg 			{width: 36px; height: 36px;}

	&--xl 			{width: 50px; height: 50px;}

	&--xxl 			{width: 100px; height: 100px;}


	/* ROTATE */

	&--rotate-90 	{transform: rotate(90deg);}

	&--rotate-180 	{transform: rotate(180deg);}
	
	&--rotate-270  	{transform: rotate(270deg);}
	
	&--flip-x 		{transform: scaleX(-1);}

	&--flip-y 		{transform: scaleY(-1);}

	&.icon-checkmark {fill: #fff;}
}

.ums-icon-wrap {
	display: flex;
	align-items: center;
	
	& > * + * {
		margin-left: 12px;
	}
}