

// POPUP 

.ums-popup {

	&__enter 		{position: relative;
		&--flex 	{display: flex;}
	}

	&__infoicon		{color: @color-base; position: relative; top: 3px;}

	&__holder 		{display: none; position: absolute; top: auto; bottom: 100%; transform: translateY(-10px); left: -99999px; right: -99999px; z-index: 11; width: auto; min-width: 100px; max-width: 180px; background: @color-base; color: @color-white; .fz-sm(); line-height: 1.4; font-weight: @fw-bold; text-align: center; margin: 0 auto !important; padding: 5px 10px; border-radius: @br-base; box-shadow: @bs-base; white-space: pre-line;

		&:after 		{content: ""; z-index: -1; position: absolute; top: auto; bottom: -2px; left: 0; right: 0; width: 15px; height: 15px; display: block; margin: 0 auto; background: @color-base; transform: rotate(45deg); border-radius: @br-base/2;} 
	
		&--left			{left: -5px; right: auto; margin: 0 !important; text-align: left;}

		&--left:after	{left: 5px; right: auto;}

		&--wide			{width: 100%; max-width: 100%;}

		&--small 		{width: 100px;}
		
		&--large 		{width: 250px;}

		&--offset 		{bottom: calc(100% + 7px);}

		&--no-pointer 	{pointer-events: none;}

		&--auto-whitespace 	{white-space: normal;}

		&--break 	{overflow-wrap: break-word;}

		&--inline > span 	{display: inline;}
		
	}

	/* hover */

	&__enter:hover .ums-popup__holder {display: block;}
 
}
