// DEFAULTS

.umsPaddingTop 			{padding-top: @m-small;}
.umsPaddingBottom 		{padding-bottom: @m-small;}

@media @min1600 {
	.umsPaddingTop 		{padding-top: @m-base;}
	.umsPaddingBottom 	{padding-bottom: @m-base;}
}

.umsSmallOnly 		{}
.umsLargeOnly 		{display: none;}

@media @min1300 {
	.umsSmallOnly 		{display: none !important;}
	.umsLargeOnly 		{display: flex;}
}



// ELEMENTS

.umsDescription,
.umsDescription > * 	{.fz-md(); font-weight: 400; color: @color-black !important;}

.umsHeading 			{margin: 0;}

.umsFzLg 				{.fz-lg();}
.umsFzXl 				{.fz-xl();}

.umsTaR 				{text-align: right;}
.umsJustifyCenter 		{justify-content: center;}
.umsJustifyEnd 			{justify-content: flex-end;}

.umsOpacity50 			{opacity: .5;}

.umsLabel 				{display: flex; flex-flow: row wrap; align-items: center; width: 100%; text-transform: uppercase; color: @color-gray !important; height: 20px; opacity: .65; .fz-xs(); font-weight: @fw-heavy; * {margin-right: 10px; opacity: 1;}}
.umsLabel-auto				{width: auto;}
.umsLabel-white				{color: #fff !important;}

.umsSmall 				{.fz-xs();}

.vh 					{position: absolute!important; clip: rect(1px,1px,1px,1px); padding: 0!important; margin: 0!important; border: 0!important; height: 1px!important; width: 1px!important; overflow: hidden; color: #000; background: none!important;}

// Overwrite

.ums-group-by-controls {position: relative; margin-right: 10px !important; height: 100%; display: flex; flex-flow: row wrap; }

.ums-group-by-controls:before 	{content: 'Group by'; position: absolute; top: -20px; left: 0; display: block; width: 100%; .fz-sm(); font-weight: @fw-bold; opacity: .35;}
.ums-filter-by-controls:before 	{content: 'Filter';}

.ums-group-by-controls .control {margin-right: 5px !important; align-items: center; padding: 8px 15px; min-height: 37px; max-height: 100% !important; display: flex; flex-flow: row wrap; border-radius: @br-base/2; background: @color-light-a; border: 1px solid @color-light-c; .fz-md(); .transition();}

.ums-group-by-controls .control.selected {background: @color-white; color: @color-base; font-weight: @fw-bold;}

.ums-group-by-controls .control:hover {background: @color-light-b; cursor: pointer; .transition;}

// Overwrite Color Swatch

.ums-colorswatch__wrapper 	{margin: -5px -5px 0 -5px;}

