.ums-tree {
	&__list {
		padding: 0;
		margin: 0;
		list-style: none;
	}
	&__item {
		position: relative;
		& + * {
			margin-top: 10px;
		}
		& > ul {
			margin-top: 10px;
			padding-left: 40px;
			& > [class*="__item"] {
				&::before {
					content: "";
					display: block;
					width: 16px;
					height: 2px;
					background: #D9D9D9;
					position: absolute;
					left: -16px;
					top: 18px;
				}
				&::after {
					content: "";
					display: block;
					width: 2px;
					background: #D9D9D9;
					position: absolute;
					left: -16px;
					bottom: 0;
					margin: auto;
					height: calc(100% + 10px);
					top: -10px;
				}
				&:last-child::after {
					height: calc(50% + 11px);
    				top: -26px;
				}
			}
		}
	}
	&__inner {
		background: #fff;
		border: 1px solid #d9d9d9;
		color: @color-main;
		font-weight: 700;
		width: 300px;
		padding: 6px @m-small;
		border-radius: @br-base / 2;
	}
}
