

// Info text

.ums-infotext			{

	&__container 		{background: @color-main; color: @color-white; padding: @m-small @m-base; margin-bottom: @m-base; border-radius: @br-base; display: flex; flex-flow: row wrap; }

	&__image 			{width: 15%;

		img 			{width: 100%;}}

	&__text 			{flex: 1 1 0; padding: 0 @m-large; 

		* 					{color: @color-white !important;}

		&:nth-of-type(1) 	{margin-top: 0 !important;}}

	&__button 			{width: auto; align-self: flex-end;}

}  