

// TYPE

.rte p, a, ul, li, small	{line-height: 1.6; color: @color-base;}
.rte a			{color: @color-graph01; text-decoration: underline; cursor: pointer; .transition;}
.rte a:hover 	{color: @color-main;}	
small 			{display: inline-block;}
p, li			{color: @color-black; .fz-md();}

// HEADING

.h1, h1, .h2, h2, .h3, h3, .h4, h4, .h5, h5, .h6, h6 {font-weight: @fw-bold; line-height: 1.4; color: @color-black;

	&--dark {color: @color-black !important;}}

.h1, h1 {.fz-xxl();}
.h2, h2 {.fz-xl(); color: @color-main;}
.h3, h3 {.fz-lg(); color: @color-main;} 
.h4, h4 {.fz-md(); color: @color-black;} 
.h5, h5 {.fz-md(); color: @color-black;} 
.h6, h6 {.fz-md(); color: @color-black;}

 