

// NAVIGATION 

.ums-nav {
	
	&__main 			{padding-top: @nav-height/1.25; min-height: 100vh;} 

	&__container 		{position: fixed; left: 0; top: @nav-height; width: 100%; z-index: @z-header; background-color: @color-main; background-image: url("@{assets}/ums-logo.svg"); background-size: auto @nav-height/1.25; background-repeat: no-repeat; background-position: top right @m-base;}

	&__list				{margin: 0; padding: 0; list-style: none; width: 100%; max-width: ~"calc(100% - 175px)"; height: @nav-height/1.25; display: flex; flex-flow: row wrap;}

	&__item 			{position: relative; display: flex; max-width: 20%; 

		*				{color: @color-white;}
		 
		&.s-active .ums-nav__button,
		&:hover .ums-nav__button	{background: darken(@color-main, 10%); color: @color-alt;}	

		&.s-active .ums-nav__icon,
		&:hover .ums-nav__icon		{fill: @color-alt;}	

		/* Tijdelijk uitzetten van tabbladen */

		&.s-disabled,
		&.s-disabled *	{color: fade(@color-white, 25%) !important; pointer-events: none;}

	}

	&__button  			{text-decoration: none; line-height: @nav-height/1.25; height: 100%; border: 0; cursor: pointer; padding: 0 @m-small - 4px 0 @m-small*2; background: @color-main; font-weight: @fw-bold; appearance: none; .transition(); word-break: none !important; white-space: nowrap !important; overflow: hidden !important; text-overflow: ellipsis !important; }

	&__icon				{position: absolute; left: @m-small - 6px; top: 0; bottom: 0; margin: auto 0; width: 15px; height: 15px; pointer-events: none; transform: translateY(-1px); .transition();}

	@media @min1600 		{	
		
		&__main 		{padding-top: @nav-height;}

		&__container 	{background-size: auto @nav-height;}

		&__list			{height: @nav-height;}

		&__item 		{max-width: 20%;}

		&__button 		{padding: 0 @m-small 0 @m-large - 5px; line-height: @nav-height;}

		&__icon			{left: @m-small; width: 20px; height: 20px;}

	}
}


// SUB NAV 

.ums-subnav {

	&__container 			{height: @nav-height/1.25; background: @color-white; border-bottom: 1px solid @color-border; display: flex; flex-flow: row; align-items: center; justify-content: space-between; height: 100%; margin: 0 auto; overflow: hidden;}

	&__list 				{margin: 0; padding: 0; list-style: none; width: 100%; position: relative; left: -@m-small - 5px; height: @nav-height; display: inline-flex; flex-flow: row wrap;} 

	&__item 				{position: relative; display: flex;

		&.s-active .ums-subnav__button 		{color: @color-main; &:after {content: ''; position: absolute; top: auto; bottom: -5px; left: 0; right: 0; margin: 0 auto; border-radius: 3px; background: @color-main; display: block; width: ~"calc(100% - " @m-small ~")"; height: 10px;}}
	
		&.s-disabled .ums-subnav__button	{color: fade(@color-gray, 50%); pointer-events: none;}}

	&__button				{text-decoration: none; color: @color-base; display: flex; flex-flow: row wrap; align-items: center; position: relative; overflow: hidden; border: 0; border-radius: 0; padding: 0 @m-small - 5px; .fz-md(); height: 100%; font-weight: @fw-bold; background: none; appearance: none; .transition();  

		&:hover 			{background: fade(@color-light-a, 75%);} 
 
	}

	&__backbutton 			{filter: grayscale(1); margin-right: 20px; color: fade(@color-gray, 75);
		& > a {color: inherit;}
		& > * > ums-icon {bottom: 1px; position: relative;}
		& > * > span:first-child {margin-right: 15px;}
		& > * > span:last-child {margin-left: 15px;}
	}

	@media @min1600 			{

		&__container		{height: @nav-height;}
		
		&__list 			{height: @nav-height; left: -@m-small;}
 
		&__item.s-active .ums-subnav__button:after  { width: ~"calc(100% - " @m-small*2 ~")";}
		
		&__button			{padding: 0 @m-small - 5px}

	}

	@media @max1650 			{
		&__button			{padding: 0 @m-small - 12px}
	}

	/* elements */

	&__form 				{display: inline-flex; align-self: flex-end;}

}

&.in-content .ums-subnav__container {
	position: sticky; z-index: 30; top: 0;
}

.as-third-nav .ums-subnav {
	&__container {
		background: transparent;
		border: 0;
		height: auto;
		& .is-animated {
			opacity: 1 !important;
			transform: none;
			animation: none !important;
		}
	}

	&__list {
		width: auto;
		background: #E3E5E9;
		border-radius: @br-base/2!important;
		padding: 2px;
		border: 1px solid #D1D4DC;
		left: 0 !important;
		height: auto;
		margin-top: @m-base;
	}
	
	&__button {
		background: transparent;
		color: #68676B!important;
		max-height: none;
		.fz-md();
		border-radius: @br-base/2!important;
		line-height: 1.2;
		font-weight: 700;
		display: flex;
		justify-content: center;
		padding: 12px @m-small;
		min-width: 120px;
		
		&::after {
			display: none !important;
		}
		
	}
	&__item:not(:first-child) > [class*="__button"] {
		margin-left: 4px;
	}
	&__item.s-active > [class*="__button"] {
		box-shadow: @bs-hover;
		color: #fff!important;
		background: #3444B2;
	}
}
