

// TOPBAR 

.ums-topbar {
		
	&__container 	{background: @color-white; .fz-md(); height: @topbar-height/1.25 - 2px; /* minus border */ border-bottom: 1px solid @color-border;}

	&__content 		{@fadeUp(); display: flex; flex-flow: row wrap; align-items: center; height: 100%;

		> * 		{flex: 1 1 0;}}

	&__title 		{display: inline-flex; align-items: center; color: @color-base; font-weight: 700; line-height: 2; .fz-xxl();}

	&__icon 		{display: inline-flex; flex-flow: row wrap; align-items: center; color: @color-base; max-width: 50px; margin-right: 10px;}

	&__back 		{max-width: 200px;}

	&__logo 		{max-width: 200px; display: none;}

	&__options 		{text-align: right; 

		button 		{margin-left: 5px;}

	}


	/* info toggle  */

	&__info 				{

		&__container 		{display: flex; flex-flow: row wrap; width: 100%; padding: @m-large @m-base; background: @color-white; border-top: 1px solid @color-border; position: relative; z-index: 1;

			&.is-hidden 	{display: none;}

			> * 			{position: relative;}

			> *:before, 
			> *:after 		{content: ''; display: block; width: 15px; height: 15px; position: absolute; top: -@m-large; left: auto; right: 80px; background: @color-white; border-top: 1px solid @color-border; border-right: 1px solid @color-border; transform: translateY(-9px) rotate(-45deg);}

		}
		
		@media @min1600 		{

			&__text 		{max-width: 90%;}

		}
	}

	@media @min1600 		{

		&__container 	{height: @topbar-height - 2px;

			&--stepbar  {height: @nav-height;}

		}
		
	}

}


// *** IN CONTENTAPP *** 

&.in-content 			{

	.ums-topbar 		{

		&__container 	{background: @color-main; color: @color-white; height: @topbar-height/1.5; display: none;}

		&__icon 		{color: @color-white;}

		&__title 		{color: @color-white; .fz-xl();}

		&__logo 		{display: block;}

	}
}
