// STARTPAGE 
.ums-infoicons {
	&__list {
		display: flex;
		flex-flow: row wrap;
		list-style: none;
		margin: 0;
		padding: 0;
		margin: @m-small 0 0;
		width: 100%;
		justify-content: flex-start;
		&--nowrap {
			flex-wrap: nowrap;
		}
	}
	&__item {
		display: inline-flex;
		align-items: center;
		color: @color-base;
		padding-right: @m-small;
		font-weight: @fw-bold;
		position: relative;
		margin: @m-small/2 0 @m-small/3;
		span {
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
			width: 100%;
			display: inline-block;
		}
		.ums-icon {
			width: 15px;
			max-width: 15px;
			max-height: 15px;
		}
		ums-icon {
			color: @color-base;
			margin-right: @m-small/3;
		}
		&--wide {
			width: 66%;
			max-width: 66%;
			min-width: 66%;
		}
		&--25 {
			width: 25%;
			max-width: 25%;
			min-width: 25%;
		}
		&--33 {
			width: 33%;
			max-width: 33%;
			min-width: 33%;
		}
		&--50 {
			width: 50%;
			max-width: 50%;
			min-width: 50%;
		}
		&--100 {
			width: 100%;
			max-width: 100%;
			min-width: 100%;
		}
		&--static {
			padding: 0;
			margin: 0;
		}
	}
	@media @min1600 {
		&__item .ums-icon {
			width: 18px;
			max-width: 18px;
			max-height: 18px;
		}
		&__item #icon-status-draft .ums-icon {
			width: 16px;
			max-width: 16px;
			max-height: 16px;
		}
	}
	&__bar {
		display: flex;
		border-top: 1px solid @color-light-c;
		padding-top: 20px;
		padding-bottom: 20px;
		color: @color-gray;
		&--margin-top {
			margin-top: -10px;
		}
	}
	&__baritem {
		display: grid;
		grid-template-areas: "icon name" ". date";
		grid-template-columns: auto minmax(0, 1fr);
		grid-template-rows: auto auto;
		grid-column-gap: 10px;
		font-weight: @fw-bold;
		&+.ums-infoicons__baritem {
			margin-left: 40px;
		}
		&>.ums-infoicons__baritem__icon {
			grid-area: icon;
			align-self: center;
		}
		&>.ums-infoicons__baritem__name {
			grid-area: name;
			.fz-md();
			max-width: 300px;
			overflow: hidden;
			white-space: nowrap;
			text-overflow: ellipsis;
		}
		&>.ums-infoicons__baritem__date {
			grid-area: date;
			.fz-sm();
			line-height: 1;
			max-width: 300px;
			overflow: hidden;
			white-space: nowrap;
			text-overflow: ellipsis;
		}
	}
}