

// Groups (Persona & Journeys)


// Table

.ums-grouptable 	{

	&__wrapper		{display: flex; flex-flow: row nowrap; width: 100%; align-items: center;}

	&__colorbar 	{flex: 0 0 auto; width: 5px; height: 40px; border-radius: @br-base; margin-right: @m-small/2; border: 1px solid @color-border; background: @color-beige;}

	&__image 		{flex: 0 0 auto; width: 40px; height: 40px; margin-right: @m-small; padding: 5px; border-radius: @br-base; border: 1px solid @color-border;}

	&__title 		{flex: 1 1 auto; word-break: break-all; word-break: break-word; font-weight: @fw-bold; display: flex; flex-flow: row nowrap; align-items: center;

		.ums-popup__enter 	{margin-left: 10px;}}

	&__scorelabel 	{width: 100%; display: flex; flex-flow: row wrap; justify-content: space-between;

		> * 		{margin: 0;}}	

	&__slider 		{

		.rz-bar-wrapper:nth-child(3) .rz-bar 			{background: @color-light-a;}
		.rzslider 			{margin: 15px 0 15px 0;}

		.rz-limit,
		.rzslider:before	{display: none;}

		.rz-pointer,
		.rz-model-value, 
		.rz-pointer:after 	{background: @color-main;}

		.rz-model-value 	{padding: 2px 5px;}
		.rz-bar-wrapper {transition: none;}
		.rz-selection { left: 1px; }
		.rz-bar-wrapper:nth-child(4):before, .rz-bar-wrapper:nth-child(3):after 	{content: ''; position: absolute; top: 8px; left: auto; right: 0; display: block; width: 12px; height: 12px; border-radius: 50%; z-index: 1; border: 4px solid @color-light-a; background: @color-white;}

		.rz-bar-wrapper:nth-child(4):before	{left: 0; z-index: 10; background: #3646b2; border-color: #fff; width: 16px; height: 16px; border-width: 2px;}

	}	 
}



// Boxes

.ums-groupbox 		{

	&__container 	{position: relative; padding: @m-small; background: @color-white; margin-bottom: @m-small; border-radius: @br-base; border: 1px solid @color-border; display: flex; flex-flow: row wrap; align-items: flex-start; box-shadow: @bs-base;
		
		&:hover 	{box-shadow: @bs-hover; cursor: pointer; .transition;}
		
		&--empty 	{display: flex; min-height: 100px; justify-content: center; align-items: center; background: fade(#000, 3); box-shadow: none; user-select: none; width: 100%; -webkit-appearance: none;

			img 	{position: absolute; height: 120%; left: auto; right: -10px;}}
 		
 		&--sm 		{padding: @m-small/2 @m-small/2 @m-small/2 @m-base; margin-bottom: -10px;}

		&--pending 	{min-height: 0;

			&:hover {box-shadow: none}}}

	&__colorbar 	{width: 5px; height: 80px; border-radius: @br-base; margin-right: @m-small/2; border: 1px solid @color-border; background: @color-beige;

		&--sm  		{height: 40px; margin-right: @m-small/3}}

	&__image 		{width: 80px; height: 80px; margin-right: @m-base; padding: @m-small/2; border-radius: @br-base; border: 1px solid @color-border;

		&--sm 		{width: 40px; height: 40px; margin-right: @m-base/2;}}

	&__text 		{flex: 1 1 0; * {.fz-md(); line-height: 1.2}}

	&__dragicon 	{position: absolute; top: 0; bottom: 0; left: auto; right: -12px; height: 24px; width: 24px; margin: auto 0; box-shadow: 0 0 0 rgba(0,0,0,0.0); display: flex; flex-flow: row wrap; justify-content: center; align-items: center; border-radius: 50%; background: @color-white; border: 1px solid @color-border; .transition;

		span,
		span:after	{width: 10px; height: 2px; background: @color-gray; display: block; position: relative; top: -2px;}

		span:after 	{content: ''; top: 4px;}}

	&__editicon 	{position: relative; color: @color-gray; top: -@m-small/2; left: @m-small/2; cursor: pointer; padding: 2px 5px; border-radius: @br-base;

		&:hover 	{background: @color-light-a;}}

	&__container:hover .ums-groupbox__dragicon {right: -20px; height: 40px; width: 40px; box-shadow: @bs-base;}

	&__checkbox 	{position: absolute; top: 4px; left: 4px; margin: 0;}

} 


// skeleton

.ums-skeleton 	{
	
	&__button 		{.fz-md(); width: 100%; height: 40px; display: flex; flex-flow: row wrap; align-items: center; justify-content: center; padding: 0 @m-small/2; border-radius: @br-base; text-align: center; border: 1px dashed @color-main; color: @color-main; background: lighten(@color-main, 51%); box-shadow: 0 0 0 rgba(0,0,0,0.0);

		span  		{margin-right: @m-small/2; text-decoration: underline; font-weight: 700; height: 100%; display: flex; align-items: center; }
		
		ums-icon 	{width: 10px !important; height: 10px !important;}

		&:hover 	{cursor: pointer; background: lighten(@color-main, 48%); box-shadow: @bs-hover; .transition;}} 

	&__hoverbox 	{width: 100%; height: 10px; font-size: 0; display: flex; flex-flow: row wrap; justify-content: center; margin: -@m-small/2 0 @m-small/2 0; .transition; 

		* 					{font-size: 0; text-align: center; height: 100%; max-height: 100%; .transition;}

		> * 				{width: 200px; .transition;}

		ums-icon 			{width: 0 !important; height: 0 !important;}

		&:hover 			{height: 40px; .transition;}

		&:hover *			{.fz-md();}

		&:hover ums-icon 	{width: 10px !important; height: 10px !important;}

	}

}  