.u-marketing-suite-dashboard, .u-marketing-suite, .ums-portal-content {
	&:not(.ums-portal-content) {margin: -20px; width: ~"calc(100% + 40px)"; background: #f6f4f4; font-family: Lato,Helvetica Neue,Helvetica,Arial,sans-serif !important;}

	* {box-sizing: border-box;}

	*:focus {outline: 0;}


	// IMPORT: BASE

	@import "base/variables.less";
	@import "base/shortcuts.less";
	@import "base/animations.less";


	// IMPORT: LAYOUT

	@import "layout/layout.less";


	// IMPORT: ELEMENTS

	@import "elements/type.less";
	@import "elements/button.less";
	@import "elements/icon.less";
	@import "elements/form.less";
	@import "elements/charts.less";
	@import "elements/loader.less";
	@import "elements/tags.less";
	@import "elements/table.less";
	@import "elements/gotoarrow.less";
	@import "elements/divider.less";


	// IMPORT: COMPONENTS

	@import "components/nav.less";
	@import "components/pagination.less";
	@import "components/alert.less";
	@import "components/topbar.less";
	@import "components/testdetail.less";
	@import "components/testadvice.less";
	@import "components/testinformation.less";
	@import "components/boxed.less";
	@import "components/testing.less";
	@import "components/legenda.less";
	@import "components/overlay.less";
	@import "components/filter.less";
	@import "components/datepicker.less";
	@import "components/rangeslider.less";
	@import "components/stepbar.less";
	@import "components/startpage.less";
	@import "components/infoicons.less";
	@import "components/abtesting.less";
	@import "components/accordion.less";
	@import "components/tree.less";
	@import "components/popup.less";
	@import "components/groups.less";
	@import "components/pagemenu.less";
	@import "components/linkbox.less";
	@import "components/contentbar.less";
	@import "components/infotext.less";
	@import "components/savebar.less";
	@import "components/segments.less";
	@import "components/fakegraph.less";
	@import "components/profile.less";
	@import "components/heatmap.less";

	// temp styling
	@import "components/contentapp.less";

	.ums-temp__holder {max-width: 700px; margin: 60px auto 40px; font-size: 20px; text-align: center;}

	.ums-temp__moretocome {min-height: 300px; display: flex; justify-content: center; align-items: center; opacity: .5;

		* {text-align: center; width: 100%; opacity: .5;}
	}
}

@import "umbraco/badges.less";



// Umbraco alerts

.umb-notifications.ums-active {left: 0 !important; right: 0 !important; margin: 0 auto !important; bottom: 10px; max-width: 90%; width: 1600px;}


.ums-portal-content {display: inline-block;
	&--fullwidth { display: flex; width: 100%;
		& > * {margin: 0 !important; width: 100%;}
	}
}