// BASE/2 LAYOUT

* {
    box-sizing: border-box;
}

.ums-container {
    width: 100%;
}

.ums-content {
    width: 100%;
    max-width: 100%;
    margin: 0 auto;
    padding-left: @m-base;
    padding-right: @m-base;

    &--small {
        padding-left: @m-small;
        padding-right: @m-small;
    }

    @media @min1600 {
        max-width: @max-width;
    }
}

.ums-headerbar {
    &__bar {
        margin-top: -1.5rem;
    }

    &__title {
        margin-top: 1.5rem;
        margin-bottom: 1.5rem;
        margin-right: 2rem;
    }

    &__options-wrapper {
        display: flex;
        justify-content: flex-start;
        align-items: center;
    }
}

// GRID: FLEX;

.ums-flexgrid {
    &__container {
        min-width: 100%;
        max-width: ~"calc(100% + " @m-base / 1.5 ~")";
        margin-left: -(@m-base / 1.5) / 2;
        margin-right: -(@m-base / 1.5) / 2;
        display: flex;
        flex-flow: row wrap;
        align-items: stretch;
        justify-content: space-between;

        &--flexstart {
            justify-content: flex-start;
        }

        &--nomargin > * {
            margin: 0 !important;
        }

        &--fullwidth {
            margin: 0 !important;
            max-width: 100% !important;
        }
    }

    &__item {
        flex: 0 0 100%;
        max-width: 100%;
        padding-left: @m-small / 2;
        padding-right: @m-small / 2;
        margin-top: @m-small;

        &--1-2 {
            flex: 0 0 50%;
            max-width: 50%;
        }

        &--2-3 {
            flex: 0 0 66.6667%;
            max-width: 66.6667%;
        }

        &--1-3 {
            flex: 0 0 33.3334%;
            max-width: 33.3334%;
        }

        &--3-4 {
            flex: 0 0 75%;
            max-width: 75%;
        }

        &--1-4 {
            flex: 0 0 25%;
            max-width: 25%;
        }

        &--2-5 {
            flex: 0 0 40%;
            max-width: 40%;
        }

        &--3-5 {
            flex: 0 0 60%;
            max-width: 60%;
        }

        &--sidebar-left {
            background: @color-white;
            z-index: 10;
            border-right: 1px solid @color-border;
            position: relative;
            margin: 0 !important;
            min-height: ~"calc(100vh - " @nav-height*3 - 15px ~")";

            &:before {
                content: "";
                display: block;
                width: 300%;
                background: @color-white;
                height: 100%;
                position: absolute;
                top: 0;
                left: -300%;
            }
        }

        &--distance-between {
            & > * + * {
                margin-top: 10px;
            }
        }

        &.s-disabled > * {
            & * {
                pointer-events: none;
            }
            opacity: 0.5;
        }

        @media @min1300 {
            &--lg-1-2 {
                flex: 0 0 100%;
                max-width: 100%;
            }

            &--lg-1-2 {
                flex: 0 0 50%;
                max-width: 50%;
            }

            &--lg-2-3 {
                flex: 0 0 66.6667%;
                max-width: 66.6667%;
            }

            &--lg-1-3 {
                flex: 0 0 33.3334%;
                max-width: 33.3334%;
            }

            &--lg-3-4 {
                flex: 0 0 75%;
                max-width: 75%;
            }

            &--lg-1-4 {
                flex: 0 0 25%;
                max-width: 25%;
            }

            &--lg-2-5 {
                flex: 0 0 40%;
                max-width: 40%;
            }

            &--lg-3-5 {
                flex: 0 0 60%;
                max-width: 60%;
            }
        }

        &--wrap-padding {
            &[class*="__item--1-3"]:nth-of-type(n + 4) {
                padding-top: 30px;
            }
        }
    }

    @media @min1300 {
        &__container {
            max-width: ~"calc(100% + " @m-base ~")";
            margin-left: -@m-base / 2;
            margin-right: -@m-base / 2;
        }

        &__item {
            padding-left: @m-base / 2;
            padding-right: @m-base / 2;
            margin-top: @m-base;
        }
    }

    /* modified */

    &__container--sm {
        max-width: ~"calc(100% + " @m-small*1.5 ~")";
        margin-left: -(@m-small*1.5) / 2;
        margin-right: -(@m-small*1.5) / 2;
    }

    &__container--sm > .ums-flexgrid__item {
        padding-left: @m-small / 3;
        padding-right: @m-small / 3;
        margin-top: @m-small / 1.5;
    }

    &__container--lg {
        max-width: ~"calc(100% + " @m-base ~")";
        margin-left: -@m-base / 2;
        margin-right: -@m-base / 2;
    }

    &__container--lg > .ums-flexgrid__item {
        padding-left: @m-small;
        padding-right: @m-small;
    }

    &__container--lg > .ums-flexgrid__item--sidebar-left {
        padding-right: @m-base;
        & + * {
            padding-left: @m-base;
        }
    }

    @media @min1300 {
        &__container--sm {
            max-width: ~"calc(100% + " @m-base*1.5 ~")";
            margin-left: -(@m-base*1.5) / 2;
            margin-right: -(@m-base*1.5) / 2;
        }

        &__container--sm > .ums-flexgrid__item {
            padding-left: @m-base / 3;
            padding-right: @m-base / 3;
            margin-top: @m-base / 1.5;
        }

        &__container--lg {
            max-width: ~"calc(100% + " @m-large ~")";
            margin-left: -@m-large / 2;
            margin-right: -@m-large / 2;
        }

        &__container--lg > .ums-flexgrid__item {
            padding-left: @m-base;
            padding-right: @m-base;
        }

        &__container--lg > .ums-flexgrid__item--sidebar-left {
            padding-right: @m-large;
            & + * {
                padding-left: @m-large;
            }
        }
    }
}
