

// AB TESTING 

.ums-abtesting {


	/* panel */

	&__panel					{display: flex; flex-flow: row;}


	/* state */

	&__state 					{width: 100px; margin-right: @m-base; text-align: center; 

		&__icon					{position: relative; width: 100%; margin: -@m-small 0;
			
			ums-icon 			{width: 100%; height: 100%; opacity: .15;}

			ums-icon + ums-icon {position: absolute; width: 30px; height: 30px; top: 45px; left: 0; right: 0; margin: 0 auto; opacity: 1; z-index: 10;}

			&.is-running 		{color: @color-graph01;}
			&.is-finished 		{color: @color-gray;}
			&.is-completed 		{color: @color-success;}			

		}

		&__title				{position: absolute; top: 80px; left: 0; width: 100%; text-align: center; font-weight: @fw-bold; .fz-md();}

		&__subtitle				{display: block; width: 120%; margin-left: -10%; color: @color-black; font-weight: @fw-bold; opacity: .5; .fz-sm();}

		&__date					{display: block; color: @color-base; font-weight: @fw-bold; .fz-md();}

		@media @min1600 			{margin-right: @m-base*1.5;}

	}


	/* content */

	&__content 					{flex: 1; padding-right: @m-base; display: flex; flex-flow: column wrap; justify-content: space-between;}


	/* graph */

	&__graph 					{width: 200px; margin-top: @m-base + 12px;

		&__item 				{display: flex; flex-flow: row; align-items: center; width: 100%; margin-bottom: @m-small/2; padding: 0 @m-small 0 @m-small/2; border-radius: @br-base*2;

			&.is-best 			{background: @color-main; 				.ums-abtesting__graph__inner 	{background: @color-graph02;} 	* {color: @color-white;}}
			&.is-worst 			{background: fade(@color-error, 15%); 	.ums-abtesting__graph__line 	{background: @color-white;}}

		}

		&__text 				{font-weight: @fw-bold; color: @color-black; width: 25px; .fz-md()}

		&__line 				{position: relative; flex: 1; height: 5px; border-radius: @br-base; background: @color-border; margin: 0 @m-small/2 0 0; overflow: hidden;}

		&__inner				{position: absolute; left: 0; top: 0; height: 100%; border-radius: @br-base;

			&.is-positive 		{background: @color-success;}
			&.is-negative 		{background: @color-error;}

		}

	}

}