// GOTO ARROW 
.ums-gotoarrow__list {
	display: flex;
	position: absolute;
	left: auto;
	top: 5px;
	right: 5px;
	margin: 0 !important;
	&>.ums-gotoarrow {
		position: relative;
		top: 0;
		right: 0;
		border-radius: 0 3px 0 3px;
		&+.ums-gotoarrow {
			margin-left: 10px !important;
		}
	}
}

.ums-gotoarrow {
	color: @color-gray;
	background: @color-beige;
	min-width: 25px;
	height: 25px;
	position: absolute;
	left: auto;
	top: 5px;
	margin: 0 !important;
	right: 5px;
	padding: 1px 6px;
	border-radius: 0 @br-base 0 @br-base;
	transform-origin: top right;
	border: 0;
	display: flex;
	align-items: center;
	justify-content: center;
	.transition;
	&:hover, &:focus {
		text-decoration: none;
		background: @color-main;
		color: @color-white !important;
		ums-icon {
			color: @color-white !important;
		}
		.ums-icon {
			fill: @color-white !important;
		}
	}

	span {
		font-weight: 700;
		text-transform: uppercase;
		font-size: 11px;
		padding-left: 6px;
		padding-right: 6px;
		& + ums-icon {
			position: relative; 
			top: -1px;
		}
	}
	ums-icon {
		transform: rotate(0);
		width: 10px;
		height: 10px;
		.transition;
	}
}

// ANIMATE WHEN IN LINK
a:hover .ums-gotoarrow,
button:hover .ums-gotoarrow {
	background: @color-main;
	color: @color-white !important;
	ums-icon {
		color: @color-white !important;
	}
	.ums-icon {
		fill: @color-white !important;
	}
}