// Segments

/* BUILDER */

.ums-segmentbuilder {
  &__container {
    display: flex;
    flex-flow: column wrap;
    align-items: flex-start;
    padding: 0 !important;
    margin-top: 0 !important;
    border-left: 1px solid @color-border;
    z-index: 0;
    overflow: hidden;
  }

  &__inner-container {
    display: flex;
    flex-flow: column wrap;
    flex: 1;
    width: 100%;
    height: 100%;
  }

  &__panel,
  &__form {
    width: 100%;
  }

  &__animate:not(.first) {
    animation: animateIn 0.6s cubic-bezier(0.43, 0.26, 0, 1.01) forwards;
  }

  &__animate:not(.first).prev {
    animation: animateOut 0.6s cubic-bezier(0.43, 0.26, 0, 1.01) forwards;
  }

  @keyframes animateIn {
    0% {
      opacity: 0;
      transform: scale(0.95);
    }
    100% {
      opacity: 1;
      transform: scale(1);
    }
  }

  @keyframes animateOut {
    0% {
      opacity: 0;
      transform: scale(0.95);
    }
    100% {
      opacity: 1;
      transform: scale(1);
    }
  }
}

/* STEPS */

.ums-segmentsteps {
  &__container {
    align-self: flex-start;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    padding: 0 @m-base;
    max-width: 100%;
    width: 100%;
    height: 3rem;
    align-items: center;
    border-bottom: 1px solid @color-border;
    z-index: 1;
  }

  &__holder {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center;
    flex: 1 1 0;
    margin: 0 1rem;
    height: 100%;
  }

  &__step {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center;
    border: 0;
    background: none;
    margin: 0 0 0 2rem;
    font-weight: 700;
    position: relative;
    height: 100%;
    .fz-md();

    &:before {
      content: "";
      display: block;
      position: absolute;
      top: auto;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 3px;
      border-radius: 3px 3px 0 0;
      background: @color-white;
    }

    &:hover:not([disabled]),
    &.s-active {
      color: @color-main;
      &:before {
        background: @color-main;
      }
      .transition();
    }
  }
}

/* PARAMETER */

.ums-segmentparameter {
  &__holder {
    flex: 1 1 0;
    display: flex;
    flex-direction: column;
    position: relative;
    min-height: 380px;
    width: 100%;
  }

  &__container {
    flex: 1 1 0;
    overflow-y: scroll;
    overflow-x: hidden;
    padding: 0 @m-small / 4 @m-base @m-small;
    min-height: 380px;
    width: 100%;
  }

  &__wrap {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    padding: @m-base+5px @m-small @m-small+5px !important;
    &.s-applied {
      border: 2px solid @color-main;
    }
    &[disabled] {
      opacity: 0.5;
      box-shadow: none !important;

      & [class*="__title"] {
        color: @color-base;
      }

      & [class*="gotoarrow"] {
        opacity: 0;
      }
    }
  }

  &__parameter-soon,
  &__parameter-label {
    position: absolute;
    top: -2px;
    left: 12px;
    opacity: 0.8;
    font-size: 10px;
    color: @color-base;
    text-transform: uppercase;
    font-weight: 800;
    display: block;
  }
  &__parameter-soon {
    width: 100%;
    text-align: center;
    opacity: 1;
    left: 0;
  }

  &__label {
    margin: @m-base 0 -@m-small !important;
  }

  &__icon {
    color: @color-base;
  }

  &__title {
    font-weight: @fw-bold;
    color: @color-main;
    text-align: center;
    width: 100%;
    .fz-md();
    line-height: 1.2;
  }

  &__button-holder {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    background: #fff;
    border-top: 1px solid #d1d4dc;
    padding: 13.33333333px 30px !important;
  }

  &__datepicker {
    display: flex;
  }

  &__datepicker-input {
    flex: 1 1 auto;
    height: 40px;
    width: auto !important;
    & input {
      width: 100%;
      height: 100%;
      background: #fff;
    }
  }
  &__datepicker-btn {
    flex: 0 0 auto;
    height: 40px;
    width: 40px;
    margin-left: 10px;
    background: #eeeef0;
    border: 1px solid #d1d4dc;
    border-radius: 2.5px;
    transition: background 0.2s ease-out;
    &:hover {
      background: lighten(#eeeef0, 10);
    }
    & > * {
      position: relative;
      top: 2px;
      left: 1px;
    }
  }

  &__groupbox {
    position: relative;
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
    width: 100%;

    &--padding {
      padding-left: 40px;
    }

    & > .ums-groupbox__colorbar,
    & > .ums-groupbox__image {
      flex: 0 0 auto;
    }

    & > .ums-groupbox__image {
      padding: 5px;
      & > img {
        position: relative;
        top: -1px;
      }
    }

    & > .ums-groupbox__text {
      flex: 1 1 auto;
      overflow: hidden;
      text-overflow: ellipsis;
      top: 2px;
      position: relative;
      & > * {
        white-space: nowrap;
      }
    }

    &::before {
      content: "";
      pointer-events: none;
      width: ~"calc(100% + 2px)";
      height: ~"calc(100% + 2px)";
      position: absolute;
      left: -1px;
      right: 0;
      z-index: 10;
      top: -1px;
      border-radius: 5px;
      box-sizing: border-box;
      opacity: 0;
      transition: opacity 0.4s ease;
      border: 2px solid @color-main;
    }
    &__checkbox {
      position: absolute;
      top: 10px;
      left: 10px;
    }
    &.s-selected {
      border-color: transparent;
      &::before {
        opacity: 1;
      }
    }
    & > input.ums-form__input,
    select.ums-form__select {
      padding-top: 5px !important;
      padding-bottom: 5px !important;
      width: auto;
    }

    &--start {
      align-items: flex-start;
      height: auto;
      min-height: 39px;
      & > input,
      select {
        margin-top: 6px;
      }
      & > input[type="checkbox"] {
        margin-top: 13px;
      }
    }
  }

  &__config-parameter-holder {
    display: flex;
    flex-wrap: wrap;
    height: 100%;
  }

  &__custom-input-holder {
    display: flex;
    align-items: center;
  }

  &__custom-input-item {
    padding: 5px 10px !important;
    margin-right: 5px !important;
    &.s-large {
      width: 160px !important;
    }
  }

  &__custom-input-spacer {
    padding: 0 8px 0 4px;
    font-size: 12px;
    text-transform: lowercase;
    font-weight: 600;
    opacity: 0.6;
  }

  &__accordion-content {
    padding-top: 8px;
    padding-bottom: 8px;
    background: @color-light-a;
  }

  &__clear-button {
    padding-left: 10px;
    padding-right: 10px;
    height: 27px;
    margin: 6px 0px 6px 10px;
  }
}

/* INFOBOX */

.ums-segmentinfo {
  &__container {
    align-self: flex-end;
    display: flex;
    flex-flow: row wrap;
    border-top: 1px solid @color-border;
    background: @color-light-a;
    padding: @m-small / 1.5 @m-base !important;
    max-width: 100%;
    width: 100%;
    position: relative;
    .fz-md();

    &--blue {
      background: @color-main;
      color: @color-white;
      border-color: @color-main;
    }

    &--blue * {
      color: @color-white;
    }

    &:before {
      content: "";
      display: block;
      transform: rotate(45deg);
      width: 1rem;
      height: 1rem;
      position: absolute;
      top: -0.25rem;
      left: @m-base;
      border-radius: 3px;
      background: @color-light-a;
    }

    &--blue:before {
      background: @color-main;
    }
  }

  &__text {
    flex: 1 1 0;

    strong {
      margin-bottom: 0.25rem;
      display: block;
    }

    p {
      color: @color-white;
    }
  }

  &__image {
    width: 25%;
    position: absolute;
    top: -1.5rem;
    max-height: 80%;
    left: auto;
    right: @m-base;
    text-align: right;
  }

  &__parameter {
    &__container {
      width: 100%;
      display: flex;
      flex-flow: row wrap;
      &--tight {
        & .ums-segmentinfo__parameter__box {
          max-width: 100%;
          & > .ums-popup__enter {
            width: 100%;
          }
        }
        & .ums-segmentinfo__parameter__text {
          max-width: 100%;
          overflow: hidden;
        }
        &-margin {
          margin: -0.4rem 0 -0.8rem;
        }
      }
    }

    &__box {
      margin-right: 5px;
      border-radius: @br-base;
      border: 1px solid @color-border;
      display: inline-flex;
      flex-flow: row wrap;
      margin-bottom: 5px;
      white-space: nowrap;
      position: relative;
      background: #fff;
      color: #000;
      min-height: 2rem;

      & .ums-segmentinfo__parameter__icon {
        width: 1.6rem;
        height: 1.6rem;
        color: inherit;
        margin-top: 1px;
        & svg {
          width: 16px;
          height: 16px;
        }
        & img {
          // For custom rules, we do expect black icons. We show it in black here.
          filter: invert(0);
        }
      }

      & .ums-segmentinfo__parameter__text {
        padding: 5px 10px 5px 5px;
        line-height: 1.5;
        font-size: 0.9em;
        color: inherit;
        font-weight: 600;
      }

      & .ums-segmentrule__wrapper {
        &:not(:last-child),
        & > *:not(:last-child) {
          margin-right: 0.25rem;
        }
      }

      &.has-edit-button {
        padding-right: 1.5rem;
      }

      &--current {
        border: 0;
        background-color: rgba(0, 0, 0, 0.08);
        background-image: repeating-linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2) 9px, transparent 9px, transparent 20px, rgba(0, 0, 0, 0.2) 20px), repeating-linear-gradient(90deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2) 9px, transparent 9px, transparent 20px, rgba(0, 0, 0, 0.2) 20px), repeating-linear-gradient(180deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2) 9px, transparent 9px, transparent 20px, rgba(0, 0, 0, 0.2) 20px), repeating-linear-gradient(270deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2) 9px, transparent 9px, transparent 20px, rgba(0, 0, 0, 0.2) 20px);
        background-size: 2px calc(100% + 20px), calc(100% + 20px) 2px, 2px calc(100% + 20px), calc(100% + 20px) 2px;
        background-position: 0 0, 0 0, 100% 0, 0 100%;
        background-repeat: no-repeat;
        animation: borderAnimation 0.8s infinite linear;
        & .ums-segmentinfo__parameter__icon *,
        & .ums-segmentinfo__parameter__text * {
          fill: currentColor;
          color: rgba(0, 0, 0, 0.5);
        }
        & .ums-segmentinfo__parameter__icon {
          & img {
            // For custom rules, we do expect black icons. We show it in black here.
            filter: invert(0);
          }
        }
        & [class*="negation"] {
          margin-top: 7px;
        } 
      }
    }

    &__icon {
      display: inline-flex;
      margin-left: 5px;
      height: 2.5rem;
      width: 2.5rem;
      flex-flow: row wrap;
      align-items: center;
      justify-content: center;
      color: @color-white;
      border-radius: @br-base;
    }

    &__negation {
      background: @color-error;
      color: #fff;
      font-weight: 600;
      padding: 2px 4px;
      line-height: 1;
      border-radius: @br-base;
      height: auto;
      margin-top: 6px;
      margin-bottom: auto;
    }

    &__text {
      color: @color-white;
      .fz-md();
      flex: 1 1 0;
      display: flex;
      flex-flow: row nowrap;
      justify-content: flex-start;
      align-items: center;
      padding: 0.8rem 2.3rem 0.7rem 0.5rem;
      line-height: 1.4;
      height: auto;
    }

    &__button {
      position: relative;
      right: 0;
      top: 0;
      background: transparent;
      height: 100%;
      width: 1.75rem;
      border-radius: 0 @br-base 0 @br-base;
      border: 0;
      padding: 0.5rem;
      display: inline-flex;
      flex-flow: row wrap;
      align-items: center;
      justify-content: center;
      margin-left: -5px;

      ums-icon {
        transform: translate(1px, -1px);
      }
    }

    &__popholder {
      position: absolute;
      right: 0;
      top: 0;
      left: auto;
      bottom: auto;
      height: 1.75rem;
      width: 1.75rem;
    }
  }
}

.ums-segmentrule {
  &__display {
    display: flex;
    flex-flow: row wrap;
  }
  &__wrapper {
    display: flex;
    flex-flow: row wrap;
    &:not(:last-child),
    & > *:not(:last-child) {
      margin-right: 0.4rem;
    }
    &--thin {
      &:not(:last-child),
      & > *:not(:last-child) {
        margin-right: 0.25rem;
      }
    }
  }
  &__divider {
    opacity: 0.5;
    display: inline-flex;
  }
  &__rulecontent {
    display: inline-flex;
  }
}

@keyframes borderAnimation {
  from {
    background-position: 0 0, -20px 0, 100% -20px, 0 100%;
  }
  to {
    background-position: 0 -20px, 0 0, 100% 0, -20px 100%;
  }
}
