// BOXES 
.ums-boxed {
	/* Container */
	&__container {
		display: block;
		text-align: left;
		padding: @m-small;
		position: relative;
		background: @color-white;
		border: 1px solid @color-border;
		border-radius: @br-base;
		text-decoration: none !important;
		&+.ums-boxed__container {
			margin-top: @m-base;
		}
		&--fullheight {
			height: 100%;
		}
		&--fresh {
			border-color: #fff;
			padding: @m-base;
			box-shadow: 0 2px 3px fade(@color-black, 10%)!important;
		}
		&--flex {
			display: flex;
		}
		&--archived {
			border: 2px dotted @color-border;
			background: fade(@color-white, 25%);
			* {
				color: @color-gray;
			}
		}

		&--active {
			background: @color-main;
			color: #fff;
		}
		>*+* {
			margin-top: @m-small/2
		}
		@media @min1300 {
			& {
				padding: @m-base @m-large/1.5;
			}
			&--fresh {
				padding: @m-base+(@m-small/2) @m-base + @m-small;
			}
		}
	}
	&__clicklayer {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
	}
	&__number {
		display: inline-flex;
		width: 24px;
		height: 24px;
		align-items: center;
		justify-content: center;
		border: 2px solid currentColor;
		border-radius: 50%;
		font-size: 0.8em;
		font-weight: 900;
		top: -1px;
		margin-right: 5px !important;
		& > span:not([class]) {
			top: 1px;			
		}
	}
	/* Split box */
	&__split {
		width: 100%;
		margin: 0;
		max-width: ~"calc(50% -  "@m-small/2 ~")";
		display: inline-flex;
		flex-flow: row wrap;
		border-right: 0;
	}
	&__split+.ums-boxed__split {
		padding-left: @m-small;
	}
	&__split--autofill {
		max-width: none;
		flex: 1 1 auto;
		padding-left: 0 !important;
		&+.ums-boxed__split--autofill {
			padding-left: 20px !important;
			margin-left: 20px;
			border-left: 1px solid @color-border;
		}
	}
	@media @min1300 {
		&__split {
			max-width: ~"calc(50% -  "@m-large/3 ~")";
		}
		&__split+.ums-boxed__split {
			border-right: 0;
			padding-left: @m-large/1.5;
		}
		&__split--autofill {
			max-width: none;
			padding-left: 0;
			&+.ums-boxed__split--autofill {
				border-left: 1px solid @color-border;
			}
		}
	}
	/* Header */
	&__header {}
	/* Content */
	&__content {
		flex: 1;
		height: 100%;
		display: flex;
		flex-direction: column;
		&>* {
			margin-top: 0;
			margin-bottom: 0;
			&+* {
				margin-top: 10px;
			}
			&:nth-last-child(2) {
				flex: 1;
			}
		}
		
	}
	/* Picture */
	&__picture {
		width: 40%;
		margin: 0;
		&__img {
			width: 100%;
			height: auto;
		}
	}
	&__flexlayout {
		display: flex;
		flex-direction: column;
		height: 100%;
		&--row {
			flex-direction: row;
			align-items: center;
			&>*+* {
				margin-left: 10px;
			}
		}
	}
	&__flexsection {
		&--nomargin * {
			margin: 0;
			&.on-top {
				margin-left: 5px !important;
			}
		}
		&+[class*="__flexsection"] {
			margin-top: 20px;
		}
		&--divided {
			&+[class*="--divided"] {
				padding-top: 20px;
				border: 0;
				border-top: 1px solid #f3f3f5;
			}
		}
		&--grow {
			flex: 1 0 auto;
		}
	}
}


/* hover animation */

.ums-boxed__container {
	box-shadow: @bs-base;
	.transition;
}

button.ums-boxed__container:hover,
a.ums-boxed__container:hover,
.ums-boxed__container--clickable:hover {
	cursor: pointer;
	box-shadow: @bs-hover;
	.transition;
}