

// TAGS

.ums-tags 			{display: none; float: left; margin: 2px 5px 2px 0; padding: 6px 20px; border-radius: 25px; font-weight: @fw-bold; .fz-sm(); color: @color-white; background: deeppink;

	&--waiting 		{display: inline-block; background: @color-beige; color: @color-gray;}

	&--running 		{display: inline-block; background: @color-graph01;}

	&--warning 		{display: inline-block; background: @color-warning;}

	&--disabled 	{display: inline-block; background: @color-gray;}

	&--stopped 		{display: inline-block; background: @color-gray;}

	&--reliable 	{display: inline-block; background: @color-success;}
	
	&--leading 		{display: inline-block; background: @color-conversion; width: 25px; height: 25px; position: relative; padding: 6px 12.5px;

		* 			{position: absolute; top: 0; bottom: 0; left: 0; right: 0; margin: auto;}

	}

}