// ALERTS

.ums-alert {
  &__holder {
    display: flex;
    flex-flow: row nowrap;
    gap: 5px;
    max-height: 100px;
  }

  &__container {
    .fz-sm();
    display: flex;
    flex-flow: row wrap;
    align-items: flex-start;
    width: 100%;
    border-radius: @br-base;
    padding: @m-small / 2 @m-base / 2;
    text-align: center;
    box-shadow: @bs-base;
    margin: @m-base / 2 0;
    justify-content: center;
    line-height: 1.4;

    &--warning {
      border: 1px solid fade(@color-warning, 50%);
      color: @color-warning;
      background: lighten(@color-warning, 32%);
      font-weight: @fw-bold;
    }
    &--error {
      border: 1px solid fade(@color-error, 50%);
      color: @color-error;
      background: lighten(@color-error, 40%);
      font-weight: @fw-bold;
    }
  }

  &__content {
    flex: 1;
    padding-right: @m-base;
    margin-bottom: -@m-small / 2;
  }
}
