.ums-accordion {
	&__header {
		.fz-md();
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: space-between;
		background: @color-light-a;
		border: 1px solid @color-light-c;
		font-weight: bold;
		height: 40px;
		padding: @m-small/2 + 1px @m-base/2;
		border-radius: @br-base / 2;
		cursor: pointer;

		&[data-panel-open="true"] {
			border-bottom-left-radius: 0;
			border-bottom-right-radius: 0;
			[class*="__caret"] {
				transform: rotate(-180deg);
				color: @color-gray;
			}
		}
		&--personalisation {
			border-radius: @br-base;
			padding: 5px 20px 5px 30px;
			height: 60px;
			background: #f6f4f5;
			.transition;
			&[data-panel-open="true"] {
				background: #fff;
				&:hover { 
					background: #fff;
					& .ums-button {
						background: #E3E5E9;
					}
				}
			}
			&[data-panel-open="false"] {
				&:hover {
					box-shadow: @bs-hover;
					background: darken(#f6f4f5, 2%);
					& ~ .ums-accordion__content .ums-accordion__inner-fluid {
						background: darken(#f6f4f5, 2%);
					}
				}
				& [class*="__name"] {
					color: #68676b;
				}
			}
		}

		&--fluid {
			&[data-panel-open="true"] {
				border-bottom: 0;
			}
			& + .ums-accordion__content {
				background: @color-light-a;
				padding-top: 5px;
			}
			&:hover,
			&:focus { 
				& + .ums-accordion__content  {
					background: darken(@color-light-a, 5);
				}
			}
		}

		&:hover,
		&:focus {
			background: darken(@color-light-a, 5);
		}
	}
	&__name {
		.transition;
	}
	&__flex-holder {
		display: flex;
		align-items: center;

		& > * + * {
			margin-left: @m-base/2;
		}
		&--small {
			& > * + * {
				margin-left: @m-base/3;
			}
		}
	}
	&__added {
		color: fade(@color-gray, 25);
		.transition;
		& > span {
			line-height: 1;
			position: relative;
			top: 1px;
		}
		&.s-active {
			color: @color-success;
		}
	}
	&__caret {
		opacity: 0.5;
		color: @color-gray;
		border-radius: 50%;
		display: flex;
		align-items: center;
		justify-content: center;
		transform: rotate(0deg);
		.transition;
	}
	&-blank__caret {
		.transition;
	}
	&__content {
		border: 1px solid @color-light-c;
		border-top: 0;
		border-radius: 0 0 @br-base / 2 @br-base / 2;
		padding: @m-base/2;
		&--personalisation {
			padding: 0;
			border-radius: 0;
			& > * > .ums-table__table {
				border: 0;
			}
		}
	}
}
ums-accordion-item {
	display: block;
	& + ums-accordion-item {
		margin-top: 10px;
	}
}
