

// BUTTON 

.ums-button 	{appearance: none; border: 1px solid deeppink; border-radius: @br-base/2; line-height: 1.1 !important; outline: 0; color: @color-base; padding: @m-small/3 @m-base/3; display: inline-flex; align-items: center; font-weight: @fw-bold; cursor: pointer; text-decoration: none !important; .fz-md(); transition: background .2s ease-in-out, color .2s ease-in-out;

	&:hover, &:focus  		{outline: none;} 


	/* ICONS */

	.ums-icon--btn			{max-width: 13px; max-height: 13px; width: 100% !important; height: 100% !important;}
	
	* + ums-icon 			{margin-left: @m-small/2;}

	ums-icon + * 			{margin-left: @m-small/2;}

	.ums-icon--rotate-180 	{transform: scaleX(-1);}

	& + .ums-button 		{margin-left: 5px;}

	&--icon > span 			{position: relative; top: 1px;}

	/* COLOR */ 

	&--base 				{background: @color-base; color: @color-white; border-color: @color-base;

		&:hover, &:focus  	{background: lighten(@color-base, 5%); color: @color-white;}

	}

	&--base-trans			{background: fade(@color-base,25%); color: @color-white; border-color:  fade(@color-base, 25%);

		&:hover, &:focus  	{background: lighten(@color-base, 5%); color: @color-white;}

	}

	&--beige 				{background: @color-beige; color: @color-gray; border-color: @color-beige;

		&:hover, &:focus  	{background: lighten(@color-beige, 5%); color: @color-gray;} 

	}

	&--main 				{background: @color-main; color: @color-white; border-color: @color-main;

		&:hover, &:focus  	{background: lighten(@color-main, 5%); color: @color-white;}

	}

	&--line,
	&--light 				{background: @color-light-a; color: @color-base; border-color: @color-light-c;

		&:hover, &:focus  	{background: @color-light-b; color: @color-base;}

	}

	&--line-success 		{background: fade(@color-success, 5%); color: @color-success; border-color: @color-success;

		&:hover, &:focus  	{background: fade(@color-success, 0%); }

	}
 
	&--error 				{background: @color-error; color: @color-white; border-color: @color-error;

		&:hover, &:focus  	{background: darken(@color-error, 10%); border-color: darken(@color-error, 10%);}

	}

	&--success 				{background: @color-success; color: @color-white; border-color: @color-success;

		&:hover, &:focus  	{background: darken(@color-success, 5%); color: @color-white;}

	}

	&--line-w 				{background: none; color: @color-white; text-decoration: underline; border: none;

		&:hover, &:focus  	{background: fade(@color-white, 5%);}
 
	}

	&--link 				{background: transparent; color: @color-base; border: none; padding: 0 !important; text-decoration: underline !important; 

		&:hover, &:focus  	{background: transparent; color: @color-main; box-shadow: none;}

	}

	&--link-light 			{background: transparent; color: @color-gray; border: none; padding: 0 !important; text-decoration: underline !important; 

		&:hover, &:focus  	{background: transparent; color: @color-base; box-shadow: none;}

	} 

	&--link-lighter 		{background: transparent; color: fade(@color-gray, 65); border: none; padding: 0 !important; text-decoration: none !important; 

		&:hover, &:focus  	{background: transparent; color: @color-base; box-shadow: none; text-decoration: underline !important;}

	} 

	&--link-error			{background: transparent; color: @color-error; border: none; text-decoration: underline !important;

		&:hover, &:focus  	{background: lighten(@color-error,40%); box-shadow: none;}

	}

	&--transparent 			{background: transparent !important; border-color: transparent !important;}

	// placeholder

	&__placeholder {display: inline-flex;}


	// button wrapper

	&__wrapper 		{display: flex; margin-top: 40px; flex-flow: row wrap; justify-content: space-between; width: 100%; align-items: center;

		&--left 	{justify-content: flex-start; & > * + * {margin-left: 10px !important;}}

		&--right 	{justify-content: flex-end; & > * + * {margin-left: 10px !important;}}
		
		&--no-margin {margin-top: 0;}

		&--no-wrap {flex-wrap: nowrap;}

		&--overlay 	{margin: 0;} 

	}
 


	// MEDIA Q

	@media @min1300 		{

	 	& 					{padding: 12px @m-base/1.25;}
 
	}



	// MODIFICATIONS	
	
	&--fullwidth 			{width: 100%;}

	&--sm 					{.fz-sm(); padding: @m-small/3 @m-base/1.5;}

	&--sm-1					{.fz-sm(); padding: @m-small/3 @m-small/1.5;}

	&--square-icon 			{padding:  @m-small/2 !important; min-width: 40px; max-width: 40px !important; height: 40px; justify-content: center;}
	
	&--square-icon.ums-button--sm 	{min-width: 30px; max-width: 30px !important; height: 30px;}
 
	&--disabled 			{pointer-events: none; opacity: .35;}

	&--as-ums-bar-button 	{padding: 6px 14px; line-height: 18px !important; font-size: 14px !important;}


	// TABLE

	&--table 				{background: @color-light-a; color: @color-base; border-radius: @br-base/2; text-decoration: underline; border-color: @color-light-c; padding: 8px; margin-top: -5px; margin-bottom: -5px;

		&:hover, &:focus  	{background: @color-light-b;}} 

	&--table + .ums-button--table {margin-left: 5px;}

}


