

// TEST ADVICE

.ums-testadvice			{

	&__container 		{width: 100%; display: flex; flex-flow: row wrap; justify-content: space-between; align-items: flex-start; position: relative; margin-top: @m-small !important; /* margin-bottom: -170px; */ /* min-height: 300px; */} 
	
	&__container + .ums-testadvice__container {margin-top: 40px;}

	&__icon 			{width: @m-small*2; height: @m-small*2; background: @color-base; color: @color-white; border-radius: 50%; position: relative;}

	&__icon * 			{width: @m-small; height: @m-small; position: absolute; top: 0; bottom: 0; left: 0; right: 0; margin: auto;}

	&__textbox 			{background: @color-base; padding: @m-small/2 @m-small; border-radius: @br-base; width: ~"calc(100% - "@m-small*3 ~")"; position: relative; height: auto;}

	&__textbox:before 	{content: ""; position: absolute; top: 11px; left: -5px; display: block; background: @color-base; width: 20px; height: 20px; border-radius: 2px; transform: rotate(45deg);}

	&__textbox *		{color: @color-white !important; font-weight: @fw-bold;}

	&__textbox span,
	&__textbox strong 	{color: @color-base !important; font-weight: @fw-heavy;}
 
	&__image 			{position: absolute; top: 60px; left: -20px; width: 100px; display: none;}

	&__image * 			{width: 100%;}
	
	@media @min1300 		{

		&__image 			{display: block;}

		&__container 		{width: ~"calc(100% + "@m-small*3 ~")"; left: -@m-small*3;}

		&__icon 			{width: @m-small*2; height: @m-small*2; }

		&__icon * 			{width: @m-small; height: @m-small;}

		&__textbox 			{padding: @m-small @m-small*1.5; width: ~"calc(100% - "@m-small*3 ~")";}

		&__textbox:before 	{top: 16px;}

	}	

	@media @min1600			{

		&__container 		{width: ~"calc(100% + "@m-base*3 ~")"; left: -@m-base*3;}

		&__icon 			{width: @m-base*2; height: @m-base*2; }

		&__icon--sm 		{width: @m-base; height: @m-base;}

		&__icon * 			{width: @m-base; height: @m-base;}

		&__icon--sm *		{width: @m-small; height: @m-small;}

		&__textbox 			{padding: @m-small @m-base*1.5; width: ~"calc(100% - "@m-base*3 ~")";}

		&__textbox:before 	{top: 22px;}

		&__image 			{width: 140px;}

	}


	/* Small */

	&__small  				{

		&__container 		{position: relative; display: flex; flex-flow: row wrap; justify-content: space-between; margin-top: 30px !important;}

		&__textbox 			{.fz-md(); position: relative; color: @color-white; background: @color-base; padding: @m-small/2 @m-small; border-radius: @br-base; width: 100%; max-width: ~"calc(100% - "@m-small*3 ~")"; 

			span 			{color: @color-alt; font-weight: @fw-heavy;}

			&:before 		{content: ""; position: absolute; top: 12px; left: -3px; display: block; background: @color-base; width: 20px; height: 20px; border-radius: 2px; transform: rotate(45deg);}}

		&__icon 			{width: @m-small*2; max-width: @m-small*2; height: @m-small*2; z-index: 10;}

		&__icon * 			{width: @m-small; height: @m-small;}

	}


	/* Colours */

	&__small__container--light .ums-testadvice__small__icon,
	&__small__container--light .ums-testadvice__small__textbox,
	&__small__container--light .ums-testadvice__small__textbox *,
	&__small__container--light .ums-testadvice__small__textbox:before 	{background: @color-light-a; color: @color-base !important;}
	&__small__container--light .ums-testadvice__small__textbox span 	{color: @color-base !important;}
	&__small__container--light .ums-testadvice__small__icon 			{color: @color-base !important;}

	&__container--waiting .ums-testadvice__icon,
	&__container--waiting .ums-testadvice__textbox,
	&__container--waiting .ums-testadvice__textbox *,
	&__container--waiting .ums-testadvice__textbox:before 	{background: @color-base; color: @color-white !important;}
	&__container--waiting .ums-testadvice__textbox span 	{color: @color-alt !important;}

	&__container--running .ums-testadvice__icon,
	&__container--running .ums-testadvice__textbox,
	&__container--running .ums-testadvice__textbox *,
	&__container--running .ums-testadvice__textbox:before 	{background: @color-main; color: @color-white !important;}
	&__container--running .ums-testadvice__textbox span 	{color: @color-alt !important;}

	&__container--success .ums-testadvice__icon,
	&__container--success .ums-testadvice__textbox,
	&__container--success .ums-testadvice__textbox *,
	&__container--success .ums-testadvice__textbox:before 	{background: @color-success; color: @color-white !important;}
	&__container--success .ums-testadvice__textbox span 	{color: darken(@color-success, 20%) !important;}

	&__container--warning .ums-testadvice__icon,
	&__container--warning .ums-testadvice__textbox,
	&__container--warning .ums-testadvice__textbox *,
	&__container--warning .ums-testadvice__textbox:before 	{background: @color-warning; color: @color-white !important;}
	&__container--warning .ums-testadvice__textbox span 	{color: rgba(0,0,0,0.6) !important;}

	&__container--stopped .ums-testadvice__icon,
	&__container--stopped .ums-testadvice__textbox,
	&__container--stopped .ums-testadvice__textbox *,
	&__container--stopped .ums-testadvice__textbox:before 	{background: @color-gray; color: @color-white !important;}
	&__container--stopped .ums-testadvice__textbox span 	{opacity: .75;}

}