

// PAGINATION 

.ums-pagination {

	&__container 			{display: flex; flex-flow: row wrap; width: 100%; align-items: center; justify-content: flex-end; padding: @m-small 0 @m-large ;

		label, 
		input, 
		select 				{font-weight: @fw-bold; .fz-md(); color: @color-gray !important; line-height: 1.2 !important; margin-top: 0 !important; margin-bottom: 0 !important;}

	}

	&__col 					{display: flex; flex-flow: row wrap; align-items: center; margin-left: @m-small;}

	&__label 				{margin-right: 5px;}

	&__select				{position: relative; min-width: 70px; width: auto; height: 30px; padding: 0 @m-small*2 0 @m-small/2; border: 1px solid @color-light-c; border-radius: @br-base/2; appearance: none; background-position: right; background-size: 30px; background-repeat: no-repeat; background-image: url(data:image/svg+xml;base64,);

		&:hover, &:focus  	{outline: none; .transition;}

		&:focus 			{border-color: @color-base;}

	}

	&__input 				{width: 40px; height: 30px !important; padding: 0 @m-small/2 !important; border: 1px solid @color-light-c !important; border-radius: @br-base/2 !important; appearance: none;}

	&__button				{width: 30px; height: 30px; border: 1px solid @color-light-c; border-radius: @br-base/2; color: @color-base; background: none; margin-left: 5px;

		&:hover 			{border-color: @color-base;}

	}

}