.ums-ca-badge, .badge.ums-ca-badge {
    &__parent {
        position: relative;
    }

    &__item {
        width: 24px;
        height: 24px;
        box-sizing: border-box;
        border-radius: 50%;
        position: absolute;
        top: 6px;
        right: 6px;
        color: #fff;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 10px;

        & > * {
            width: 9px;
            height: 9px;
            position: absolute;
            top: 0;
            left: 1px;
            right: 0;
            bottom: 0;
            margin: auto;
        }
        
        &.s-scheduled {
            background: #68676b;

            & > * {
                width: 13px;
                height: 13px;
            }
        }
    }

    &__icon, &__item > svg {
        max-width: 18px;
        max-height: 18px;
        fill: currentColor;
    }
}

.ums-tree-badge {
    &__container {
        position: absolute;
        right: 50px;
        top: 0;
        bottom: 0;
        display: flex;
        align-items: center;

        & > * + * {
            margin-left: -6px;
        }

        & * {
            box-sizing: border-box;
        }
    }

    &__item {
        z-index: 1;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #6c6b6f;
        animation: showBadge 0.1s ease-out forwards;
        border-radius: 28px;
        height: 28px;
        background: #f6f4f4;
        border: 2px solid #fff;

        &:hover {
            & > [class*="__info"] {
                transition-delay: 0.1s;
                width: 100%;
                opacity: 1;
            }
        }
    }

    &__icon {
        width: 24px;
        height: 26px;
        display: flex;
        align-items: center;
        justify-content: center;
        color: inherit;
        flex: 0 0 auto;
        
        & > * {
            fill: currentColor;
            width: 16px;
            height: 16px;
            
            &.s-scheduled {
                width: 14px;
                height: 14px;
            }
        }
    }

    &__info {
        font-size: 12px;
        width: 0;
        overflow: hidden;
        opacity: 0;
        transition: all 0s ease-out;

        & > span {
            padding-left: 8px;
            padding-right: 2px;
        }
    }
}

@keyframes showBadge {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}