

// Contentbar page indicator 

.ums-contentbar 		{

	&__container 		{background: @color-white; width: 100%; height: 50px; border-bottom: 1px solid @color-border; display: flex; flex-flow: row wrap; justify-content: flex-end; align-items: center;

	+ * .ums-topbar__container {margin-top: -50px;}}

	&__badge 			{background: @color-main; font-weight: @fw-bold; color: @color-white; padding: 5px @m-small 5px @m-small/2; width: auto; height: 100%; height: 55px; position: relative; display: flex; flex-flow: row nowrap; align-items: center; flex: 0 0 auto;

		ums-icon:first-child 		{margin-right: 10px;}
		ums-icon:last-child 		{margin-left: 10px;}
		& > span 					{line-height: 1;}

 		&:before 		{content: ''; position: absolute; top: auto; bottom: 0; left: -30px; border-style: solid; border-width: 0 30px 60px 0; border-color: transparent @color-main transparent transparent;} 

 	}

} 